import { Vector as LayerVector, Vector as VectorLayer } from 'ol/layer'
import { Vector as VectorSource, TileArcGISRest } from 'ol/source'
import TileLayer from 'ol/layer/Tile'
import TileWMS from 'ol/source/TileWMS'
import XYZ from 'ol/source/XYZ'
import ImageWMS from 'ol/source/ImageWMS'
import ImageLayer from 'ol/layer/Image'
import TileImage from 'ol/source/TileImage'
import WMTS, { optionsFromCapabilities } from 'ol/source/WMTS'
import { WMTSCapabilities, WMSCapabilities, WKT as FormatWKT } from 'ol/format'
import proj4 from 'proj4'
import { register } from 'ol/proj/proj4'
import { get as getProjection } from 'ol/proj'
import { getWidth, getTopLeft } from 'ol/extent'
import WMTSTileGrid from 'ol/tilegrid/WMTS'
import WKT from 'ol/format/WKT.js'
import { Fill, Stroke, Style, Circle, Icon, Text } from 'ol/style'
import EsriJSON from 'ol/format/EsriJSON'
import Feature from 'ol/Feature'

import TileGrid from 'ol/tilegrid/TileGrid'
import { LayerType } from './TypeDefined'
import OlLayerClip from './OlLayerClip'
import LayerMaskTool from "skygis/skygis-tool/LayerMaskTool";
import axios from 'axios'

export default function OlMapServices(map,params) {
  // 是否支持离线切片
  this.isSupportOfflineTile = false
  this.map = map
  this.mapMaxExtent = null
  this.mapMaxBjFileName = 'npis_bj.txt' // bj-wkt.txt 雅长  dgs-bj.txt // 大桂山  npis_bj.txt // 南山
  if(params.initMapParams){
    if(params.initMapParams=="npis"){
      this.mapMaxBjFileName = 'npis_bj.txt' // bj-wkt.txt 雅长  dgs-bj.txt // 大桂山  npis_bj.txt // 南山
    }
  }else{
    this.mapMaxBjFileName = 'bj-wkt.txt' // bj-wkt.txt 雅长  dgs-bj.txt // 大桂山  npis_bj.txt // 南山
  }
  // proj4.defs('EPSG:4490', '+proj=longlat +ellps=GRS80 +no_defs')
  proj4.defs('EPSG:4490', 'GEOGCS["China Geodetic Coordinate System 2000",DATUM["China_2000",SPHEROID["CGCS2000",6378137,298.257222101,AUTHORITY["EPSG","1024"]],AUTHORITY["EPSG","1043"]],PRIMEM["Greenwich",0,AUTHORITY["EPSG","8901"]],UNIT["degree",0.0174532925199433,AUTHORITY["EPSG","9122"]],AUTHORITY["EPSG","4490"]]')
  register(proj4)
  this.loadTDTLayer = function(configLayer, params, callback) {
    var urls = []
    for (var i = 0; i < 8; i++) {
      // urls.push('http://t' + i + '.tianditu.gov.cn/DataServer?T=' + configLayer.url + '&x={x}&y={y}&l={z}&tk=' + '157cafe28035d365378e2a9e59c6be65')
      urls.push('http://t' + i + '.tianditu.gov.cn/DataServer?T=' + configLayer.url + '&x={x}&y={y}&l={z}&tk=' + 'c204179a3eb23b6c5d80488077bb8b47')
    }
    var tdtTitleLayer = new TileLayer({
      source: new XYZ({
        crossOrigin: 'anonymous',
        urls: urls
      })
    })
    callback(tdtTitleLayer)
    // let olLayerClip = new OlLayerClip()
    // olLayerClip.clipLayer(tdtTitleLayer)
    // this.maskLayer(tdtTitleLayer)
  }
   this.maskLayer = async function(layer){
    if( this.mapMaxExtent === null ){
      await axios.get('geodata/' + this.mapMaxBjFileName).then(res => {
        var format = new WKT()
        this.mapMaxExtent = format.readFeature(res.data)
      })
    }
     let layerMaskTool = new LayerMaskTool({
       feature:this.mapMaxExtent,
       layer:layer
     })
     layerMaskTool.setMaskActive(true)
     layerMaskTool.setMaskColor('#0A1A32C8')
  }
  /**
   * 加载天地图离线
   * @param configLayer
   * @param params
   * @param callback
   */
  this.addTDTOfflineLayer = function(configLayer, params, callback) {
    // 离线切片数据加载测试
    var maxResolution = 180.0 / 256
    var resolutions = []
    for (var i = 1; i <= 20; i++) {
      resolutions[i] = Math.pow(2, 1 - i) * maxResolution
    }

    var tilegrid = new TileGrid({
      origin: [-180, 180],
      resolutions: resolutions
    })

    var source = new TileImage({
      projection: 'EPSG:4326',
      tileGrid: tilegrid,
      tileUrlFunction: function(tileCoord, pixelRatio, proj) {
        var z = tileCoord[0]
        var x = tileCoord[1]
        var y = -tileCoord[2] - 1
        // http://10.120.20.214:8088/dt/tdt/2/1/3.png
        // return '/static/tdt/' + z + '/' + y + '/' + x + '.png'
        return 'http://'+window.location.hostname+':8088/dt/tdt/' + z + '/' + y + '/' + x + '.png'
        //return 'http://10.120.20.214:8088/dt/tdt/' + z + '/' + y + '/' + x + '.png'
      }
    })
    var tdtLayer = new TileLayer({
      source: source
    })
    callback(tdtLayer)
  }

  /**
   *  geoServer WMTS 加载
   * @param configLayer
   * @param params
   * @param callback
   */
  this.addArcGISWMTSLayer = function(configLayer, params, callback) {
    debugger
    const originParams = [-20037508.342787, 20037508.342787]
    const projection = getProjection(configLayer.proj)
    const ogcWMTSUrlGetCapabilities = configLayer.url + '?f=pjson'
    const resolutions = []
    const fullExtent = []
    fetch(ogcWMTSUrlGetCapabilities).then(function(response) {
      return response.text()
    }).then(function(data) {
      const resultData = JSON.parse(data)
      if (resultData.fullExtent) {
        fullExtent[0] = resultData.fullExtent.xmin
        fullExtent[1] = resultData.fullExtent.ymin
        fullExtent[2] = resultData.fullExtent.xmax
        fullExtent[3] = resultData.fullExtent.ymax
      }
      if (resultData.tileInfo.lods) {
        resolutions.length = 0
        resultData.tileInfo.lods.forEach((item, index) => {
          resolutions.push(item.resolution)
        })
      }
      const tileGridParam = new TileGrid({
        tileSize: 256,
        origin: originParams,
        extent: fullExtent,
        resolutions: resolutions
      })
      var tileLayer = new TileLayer({ // 使用XYZ的方式加载OpenStreetMap
        source: new XYZ({
          tileGrid: tileGridParam,
          projection: projection,
          url: configLayer.url + '/tile/{z}/{y}/{x}'
        }),
        name: configLayer.layerName
      })

      callback(tileLayer)
    })
  }
  /**
   *
   * @param configLayer
   * @param params
   * @param callback
   */
  this.addGeoServerWMTSLayer = function(configLayer, params, callback) {
    const originParams = [-180.0, 90.0]
    const projection = getProjection('EPSG:4490')
    const ogcWMTSUrlGetCapabilities = configLayer.url + '/wmts?REQUEST=GetCapabilities'
    const parser = new WMTSCapabilities()
    const resolutionsParams = []
    fetch(ogcWMTSUrlGetCapabilities).then(function(response) {
      return response.text()
    }).then(function(data) {
      const resultData = parser.read(data)
      const layer = resultData.Contents.Layer[0]
      const tileMatrixSets = resultData.Contents.TileMatrixSet
      tileMatrixSets.forEach((item, index) => {
        if (configLayer.proj === item.Identifier) {
          item.TileMatrix.forEach((_item, index) => {
            resolutionsParams.push(_item.ScaleDenominator * 0.0254 / 96)
          })
        }
      })
      const tileGridParam = new TileGrid({
        tileSize: 256,
        origin: originParams,
        extent: layer.WGS84BoundingBox,
        resolutions: resolutionsParams
      })
      const tileLayer = new TileLayer({ // 使用XYZ的方式加载OpenStreetMap
        source: new XYZ({
          tileGrid: tileGridParam,
          projection: projection,
          // url: configLayer.url + '/tile/{z}/{y}/{x}'
          tileUrlFunction: function(coordinate) {
            console.log('*************************************************showmap********************************************')
            const z = coordinate[0]
            const col = Math.abs(coordinate[1])
            const row = Math.abs(coordinate[2]) - 1
            return configLayer.url + '?SERVICE=WMTS&REQUEST=GetTile&VERSION=1.0.0&STYLE=default&FORMAT=tiles&' +
              'TILEMATRIX=' + z + '&TILECOL=' + col + '&TILEROW=' + row
          }
        }),
        name: configLayer.layerName
      })
      callback(tileLayer)
    })
  }

  this.addArcGISWMSLayer = function(configLayer, params, callback) {
    var projection = getProjection('EPSG:4326')
    projection.setExtent([-180, -90, 180, 90])
    projection.setWorldExtent([-180, -90, 180, 90])
    var _Url = configLayer.url
    var arcgisSource = new TileArcGISRest({
      url: _Url,
      projection: projection,
    })

    var arcgisMapServerLayer = new TileLayer({
      source: arcgisSource
    })
    callback(arcgisMapServerLayer)
  }

  // 解决同一个文字标注显示多个问题，使用单张切片
  this.addGeoServerWMSLayer = function(configLayer, params, callback) {
    const projection = getProjection(configLayer.proj)
    if( !params ){
      params = configLayer.cql_filter
    }
    let configLayerUrl = configLayer.url
    const ogcWMSUrlGetCapabilities = configLayerUrl + '?REQUEST=GetCapabilities'
    const parser = new WMSCapabilities()
    fetch(ogcWMSUrlGetCapabilities).then(function(response) {
      return response.text()
    }).then(function(data) {
      const capabilitie = parser.read(data)
      const layers = capabilitie.Capability.Layer.Layer
      if (layers && layers.length > 0) {
        const mLayer = layers[0]
        const mLayerName = mLayer.Name
        const mBoxs = mLayer.BoundingBox
        let mLayerExtent
        if (mBoxs && mBoxs.length > 0) {
          mBoxs.forEach(item => {
            if (item.crs !== configLayer.proj) {
              mLayerExtent = item.extent
            }
          })
        }
        var tileLayer = new ImageLayer({
          source: new ImageWMS({
            ratio: 1,
            extent: mLayerExtent,
            url: configLayerUrl,
            params: {
              'FORMAT': 'image/png',
              'VERSION': '1.1.1',
              'LAYERS': mLayerName,
              'exceptions': 'application/vnd.ogc.se_inimage'
            },
            projection: projection,
            serverType: 'geoserver',
            transition: 0,
            crossOrigin: 'anonymous'
          })
        })
        tileLayer.setExtent(mLayerExtent)
        if (params ) {
          tileLayer.getSource().updateParams({ CQL_FILTER: params })
        }
        callback(tileLayer)
      }
      console.log(capabilitie.toString())
    })
  }

  // 存在同一个文字标注显示多个问题，使用的是多个切片
  this.addGeoServerWMSLayerForTiled = function(configLayer, params, callback) {
    const projection = getProjection(configLayer.proj)
    const ogcWMSUrlGetCapabilities = configLayer.url + '?REQUEST=GetCapabilities'
    const parser = new WMSCapabilities()
    fetch(ogcWMSUrlGetCapabilities).then(function(response) {
      return response.text()
    }).then(function(data) {
      const capabilitie = parser.read(data)
      const layers = capabilitie.Capability.Layer.Layer
      if (layers && layers.length > 0) {
        const mLayer = layers[0]
        const mLayerName = mLayer.Name
        const mBoxs = mLayer.BoundingBox
        let mLayerExtent
        if (mBoxs && mBoxs.length > 0) {
          mBoxs.forEach(item => {
            if (item.crs !== configLayer.proj) {
              mLayerExtent = item.extent
            }
          })
        }
        const tileLayer = new TileLayer({
          extent: mLayerExtent,
          source: new TileWMS({
            url: configLayer.url,
            params: {
              'FORMAT': 'image/png',
              'VERSION': '1.1.1',
              tiled: true,
              'LAYERS': mLayerName
            },
            projection: projection,
            serverType: 'geoserver',
            transition: 0,
            crossOrigin: 'anonymous'
          })
        })
        if (params && params.CQL_FILTER) {
          tileLayer.getSource().updateParams({ CQL_FILTER: params.CQL_FILTER })
        }
        callback(tileLayer)
      }
      console.log(capabilitie.toString())
    })
  }
}
