      //     symbolColor:'',
      //     symbolName:'管理站'
      //   }
      // ],
      mAllSymbols: [],
      lastComponentSymbols: [],
      expand: true,
    };
  },
  created() {
    this.$nextTick(() => {
      this.mAllSymbols = this.symbols;
    });
  },
  methods: {
    expandMapComponent(isExpand) {
      this.expand = isExpand;
    },
    getSymbolStyle() {
      if (!this.expand) {
        return "transform: translateX(" + this.defaultRigth + "vw);";
      }
    },
    initSymbolStyle(symbol) {
      const color = symbol.symbolColor;
      const strokeColor = symbol.symbolStrokeColor;
      if (strokeColor) {
        return {
          "background-color": color,
          height: "0.8vw",
          "border-style": "solid",
          "border-width": "1px",
          "border-color": strokeColor,
          "margin-top":'0.2vh'
        };
      }
      return { "background-color": color, height: "0.8vw","margin-top":'0.2vh' };
    },
    showSymbols(symbols) {
      this.mAllSymbols = [];
      this.mAllSymbols.push.apply(this.mAllSymbols, this.symbols);
      if (symbols.length > 0) {
        this.mAllSymbols.push.apply(this.mAllSymbols, symbols);
        this.lastComponentSymbols = symbols;
      } else {
        this.lastComponentSymbols = [];
      }
    },
    clearBase() {
      this.symbols = [];
    },
    clearAll() {
      this.mAllSymbols = [];
    },
    refreshSymbols(configLayers) {
      this.symbols = [];
      this.refreshSymbolsPrivate(configLayers, this.symbols);
      this.mAllSymbols = [];
      this.mAllSymbols.push.apply(this.mAllSymbols, this.symbols);
      this.mAllSymbols.push.apply(this.mAllSymbols, this.lastComponentSymbols);
    },
    /**
     *
     */
    refreshSymbolsPrivate(configLayers, mSymbols) {
      configLayers.forEach((configLayer) => {
        // 获取图例
        let config = configLayer.iqueryConfig;
        if (config && config.length > 0) {
          var configuration = JSON.parse(config);
          var symbols = configuration.symbols;
          if (symbols && symbols.length > 0) {
            mSymbols.push.apply(mSymbols, symbols);
          }
        }
      });
    },
  },
};
</script>

