import request from '@/utils/request'

// 查询地图标绘列表
export function listMarker(query) {
  return request({
    url: '/gis/marker/list',
    method: 'get',
    params: query
  })
}

// 查询地图标绘详细
export function getMarker(id) {
  return request({
    url: '/gis/marker/' + id,
    method: 'get'
  })
}

// 新增地图标绘
export function addMarker(data) {
  return request({
    url: '/gis/marker',
    method: 'post',
    data: data
  })
}

// 修改地图标绘
export function updateMarker(data) {
  return request({
    url: '/gis/marker',
    method: 'put',
    data: data
  })
}

// 删除地图标绘
export function delMarker(id) {
  return request({
    url: '/gis/marker/' + id,
    method: 'delete'
  })
}

// 导出地图标绘
export function exportMarker(query) {
  return request({
    url: '/gis/marker/export',
    method: 'get',
    params: query
  })
}