<script>
import { addMarker } from "@/api/gis/marker";
import OLMapQuery from "../lib/OlMapQuery";
import OLMapDraw from "../lib/OlMapDraw";
import MeasureTool from "skygis/skygis-tool/MeasureTool";
import { getToken } from "@/utils/auth";
import MulitMapDialog from "./MulitMapDialog";
import landMark from "./landMark";
import MapExport from "./MapExport";
export default {
  name: "MapToolBar",
  components: { MulitMapDialog, landMark, MapExport },
  props: {
    defaultRight: {
      type: Number,
      default: 20,
      require: false,
    },
    isSjjc: {
      type: Boolean,
      default: false,
    },
    isShowEdit: {
      type: Boolean,
      default: false,
    },
  },
  data() {
    return {
      expand: true,
      isShowYear: true,
      isShowLayer: false,
      isShowEditTool: false,
      treeData: [],
      map: null,
      name: "",
      layerManager: null,
      olMapQuery: null,
      olMapDraw: null,
      showComponent: false,
      measureTool: null,
      form: {
        pictures: [],
      },
      rules: {
        name: [{ required: true, message: "名称不能为空", trigger: "blur" }],
      },
      dialogImageUrl: "",
      dialogVisible: false,
      uploadImgUrl:
        (window.location.hostname.indexOf("192.168.") == -1
          ? process.env.VUE_APP_BASE_API
          : process.env.VUE_APP_BASE_API_N) + "/file/upload", // 上传的图片服务器地址
      headers: {
        Authorization: "Bearer " + getToken(),
      },
      fileList: [],
      location: {
        visible: false,
        lon: null,
        lat: null,
      },
      rulesLocation: {
        lon: [
          { required: true, message: "请输入正确的经度", trigger: "blur" },
          { trigger: "blur", validator: this.validNumber },
        ],
        lat: [
          { required: true, message: "请输入正确的纬度", trigger: "blur" },
          { trigger: "blur", validator: this.validNumber },
        ],
      },
      mulitMapVisible: false,
      landMarkVisible: false,
      iconShow: true,
      exportMapVisible: false,
      isVisible: true,
    };
  },
  methods: {
    iconShowFn(b) {
      this.iconShow = !this.iconShow;
      // this.isShowYear = this.iconShow ;
      if (this.iconShow) {
        this.isShowYear = this.iconShow;
        this.$emit("onYearClick", this.iconShow);
      }
    },
    expandMapComponent(isExpand) {
      this.expand = isExpand;
    },
    getSymbolStyle() {
      if (!this.expand) {
        return "transform: translateX(" + (this.defaultRight - 1) + "vw)";
      }
    },
    onExpandClick() {
      this.expand = !this.expand;
      this.$emit("onExpandClick", this.expand);
    },
    showLayerManager() {
      this.$emit("showLayer");
    },
    initMap(map, layerManager) {
      this.map = map;
      this.layerManager = layerManager;
      this.olMapQuery = new OLMapQuery(this.map, this.layerManager);
      this.olMapDraw = new OLMapDraw(this.map, this.layerManager);
      this.measureTool = new MeasureTool({ map: this.map });
    },
    onQueryClick(commond) {
      this.clear();
      if (commond !== "poi" && commond !== "landMark") {
        this.olMapQuery.startQuery(commond, (features) => {
          this.olMapQuery.stopQuery();
          this.$parent.layerManager.clearSelectFeature();
          this.$parent.showFeatureList(features);
        });
      } else {
        if (commond === "poi") {
          this.$parent.isShowPoiQuery = !this.$parent.isShowPoiQuery;
          this.$parent.isShowlandMarkQuery = false;
        } else if (commond === "landMark") {
          this.$parent.isShowlandMarkQuery = !this.$parent.isShowlandMarkQuery;
          this.$parent.isShowPoiQuery = false;
        }
      }
    },
    onDrawClick(commond) {
      this.clear();
      this.olMapDraw.startDraw(commond, (geometry) => {
        // alert(geometry)
        this.olMapDraw.stopDraw();
        let drawType = commond;
        switch (commond) {
          case "Square":
            drawType = "Polygon";
            break;
          case "Box":
            drawType = "Polygon";
            break;
          default:
        }
        this.form.graphicsType = drawType;
        this.form.geometry = geometry;
        this.showComponent = true;
      });
    },
    save() {
      this.$refs["form"].validate((valid) => {
        if (valid) {
          if (this.form.id == null) {
            let graphicsType = null;
            addMarker(this.form).then((response) => {
              this.msgSuccess("保存成功");
              this.resetForm();
              this.showComponent = false;
              this.refreshDrawLayer();
            });
          }
        }
      });
    },
    cancel() {
      this.showComponent = false;
      this.resetForm();
    },
    resetForm() {
      this.form = {
        id: null,
        name: null,
        type: null,
        graphicsType: null,
        geometry: null,
        pictures: [],
      };
      this.fileList = [];
    },
    refreshDrawLayer() {
      this.layerManager.refreshDrawLayer("我的标绘");
    },
    clear() {
      // 测量清除
      this.measureTool.clear();
      this.measureTool.finish();
      // this.$parent.setLayerSelect([{layerName:'公园分区', oparams:'where fqmc = \'核心保护区\''}],true);
      // this.$parent.selectLayerHighLight([{layerName:'公园分区' ,oparams:'where fqmc = \'核心保护区\''}]);
      // var symbols = this.$parent.getSymbolsByLayerName('公园分区')
    },
    zoom2extent() {
      this.$parent.zoom2extent();
      // this.$parent.setLayerSelect([]);
    },
    onMeasureClick(type) {
      switch (type) {
        case "area":
          this.measureTool.area();
          break;
        case "lineString":
          this.measureTool.length();
          break;
        case "angel":
          this.measureTool.angle();
          break;
        default:
          break;
      }
    },
    handleRemove(file, fileList) {
      for (var i = 0; i < this.form.pictures.length; i++) {
        if (this.form.pictures[i].uid === file.uid) {
          this.form.pictures.splice(i, 1);
          return;
        }
      }
    },
    handlePictureCardPreview(file) {
      this.dialogImageUrl = file.url;
      this.dialogVisible = true;
    },
    // 照片上传成功
    imageSuccess(response, file, fileList) {
      let _path = response.data.url.split(":")[2];
      _path = _path.substring(_path.indexOf("/"));
      this.form.pictures.push({ path: _path, uid: file.uid });
    },
    exportMap(name) {
      this.$refs.mapExport.form.name = name;
      this.$parent.mapExport.exportMap2(
        this.$parent.mapId,
        "mExportMapImage",
        name,
        (data) => {
          this.exportMapVisible = true;
          this.$refs.mapExport.showImage(data);
        }
      );
    },
    exportForLabel(name) {
      this.$refs.landMark.form.name = name;
      this.$parent.mapExport.exportMap2(
        this.$parent.mapId,
        "mExportMapImage",
        name,
        (data) => {
          this.landMarkVisible = true;
          this.$refs.landMark.showImage(data);
        }
      );
    },
    showOrHideExportMap() {
      this.exportMapVisible = !this.exportMapVisible;
    },
    showOrHideLandMark() {
      this.landMarkVisible = !this.landMarkVisible;
    },
    showOrHideMulitMap() {
      this.mulitMapVisible = !this.mulitMapVisible;
      this.$refs.mulitMap.setVisible(this.mulitMapVisible);
    },
    showLocation() {
      this.location.visible = !this.location.visible;
    },
    toLocation() {
      // 判断定位坐标是否是在中国境内
      try {
        if (this.location.lon < 73.55 || this.location.lon > 136) {
          this.msgError("您输入的坐标不在中国范围内");
          return;
        }
        if (this.location.lat < 3.85 || this.location.lat > 53.55) {
          this.msgError("您输入的坐标不在中国范围内");
          return;
        }
        this.$parent.location(
          "POINT(" + this.location.lon + " " + this.location.lat + ")",
          true
        );
      } catch (e) {
        this.msgError("请输入合法的经纬度");
        return;
      }
    },
    validNumber(rule, value, callback) {
      const numReg = /^(\d+)(\.\d+)?$/;
      if (value && !numReg.test(value)) {
        callback(new Error("请输入数字"));
      } else {
        callback();
      }
    },
    // 图形编辑
    editTool() {},
    cutLing() {},
    dragDropEnd() {},
    editCancel() {},
  },
};
</script>

