import { Draw } from 'ol/interaction'
import { createRegularPolygon , createBox } from 'ol/interaction/Draw'
import WKT from 'ol/format/WKT'
import { Vector as VectorLayer } from 'ol/layer'
import { Vector as VectorSource } from 'ol/source'
import { Fill, Stroke, Style, Circle, Icon, Text } from 'ol/style'


/**
 * i建查询
 * @param map
 * @constructor
 */
export default function OLMapDraw(map, layerManager) {
  this.map = map
  this.layerManager = layerManager
  this.draw = null
  this.vectorSource = null
  this.vectorLayer = null
  this.startDraw = function(queryType ,callback) {
    this.initDraw(queryType,callback)
  }
  this.initDraw = function(drawType,callback) {
    var geometryFunction = null
    if (this.draw != null) {
      this.map.removeInteraction(this.draw)
    }
    switch(drawType){
      case "Square":
        drawType = 'Circle';
        // 生成规则的四边形的图形函数
        geometryFunction = createRegularPolygon(4)
        break;
      case 'Box':
        drawType = 'Circle';
        // 生成盒形状的图形函数
        geometryFunction = createBox()
        break;
      default:
        break;
    }
    this.initDrawLayer()
    this.draw = new Draw({
      type: drawType,
      source: this.vectorSource,
      geometryFunction: geometryFunction,
      style:new Style({
        // 将点设置成圆形样式
        image: new Circle({
          // 点的颜色
          fill: new Fill({
            color: '#FFFF00'
          }),
          // 圆形半径
          radius: 5
        }),
        // 线样式
        stroke: new Stroke({
          color: '#ff0000',
          lineCap: 'round',       // 设置线的两端为圆头
          width: 3
        }),
        // 填充样式
        fill: new Fill({
          color: '#FFFF0096'
        })
      })
    })
    this.map.addInteraction(this.draw)
    this.draw.on('drawend', e => {
      var geo = e.feature.getGeometry()
      var geometryWkt = new WKT().writeGeometry(geo)
      callback(geometryWkt)
    })
  }
  this.stopDraw = function(){
    if (this.draw != null) {
      this.map.removeInteraction(this.draw)
    }
    if( this.vectorSource ){
      this.vectorSource.clear()
    }
    if( this.vectorLayer ){
      this.map.removeLayer(this.vectorLayer)
    }
  }
  this.initDrawLayer = function(){
    if(this.vectorSource === null){
      this.vectorSource = new VectorSource();
      this.vectorLayer = new VectorLayer({
        source: this.vectorSource,
        // 设置图层样式
        style: new Style({
          // 将点设置成圆形样式
          image: new Circle({
            // 点的颜色
            fill: new Fill({
              color: '#F00'
            }),
            // 圆形半径
            radius: 5
          }),
          // 线样式
          stroke: new Stroke({
            color: '#0F0',
            lineCap: 'round',       // 设置线的两端为圆头
            width: 5
          }),
          // 填充样式
          fill: new Fill({
            color: '#00F'
          })
        })
      })
      this.map.addLayer( this.vectorLayer )
    }
  }
}
