import request from '@/utils/request'

// 查询小地名列表
export function listPoi(query) {
  return request({
    url: '/gis/poi/list',
    method: 'get',
    params: query
  })
}

// 查询小地名详细
export function getPoi(id) {
  return request({
    url: '/gis/poi/' + id,
    method: 'get'
  })
}

// 新增小地名
export function addPoi(data) {
  return request({
    url: '/gis/poi',
    method: 'post',
    data: data
  })
}

// 修改小地名
export function updatePoi(data) {
  return request({
    url: '/gis/poi',
    method: 'put',
    data: data
  })
}

// 删除小地名
export function delPoi(id) {
  return request({
    url: '/gis/poi/' + id,
    method: 'delete'
  })
}

// 导出小地名
export function exportPoi(query) {
  return request({
    url: '/gis/poi/export',
    method: 'get',
    params: query
  })
}