import OlMapServices from './OlMapServices'
import { ServiceType, LayerType } from './TypeDefined'
import { fromLonLat } from 'ol/proj'
import { Point,LineString } from 'ol/geom'
import Feature from 'ol/Feature'
import { Vector as VectorLayer ,Heatmap as HotLayer} from 'ol/layer'
import { Vector as VectorSource } from 'ol/source'
import { WKT as FormatWKT } from 'ol/format'
import { Fill, Stroke, Style, Circle, Icon, Text } from 'ol/style'
import {Draw, Modify, Snap} from 'ol/interaction'

export default function OlDraw (map) {
  this.map = map
  this.draw = null
  this.snap = null
  this.vectorSource = null
  this.vectorLayer = null
  this.style=new Style({
    fill: new Fill({
      color: 'rgba(255, 255, 255, 0.2)',
    }),
    stroke: new Stroke({
      color: '#ffcc33',
      width: 2,
    }),
    image: new Circle({
      radius: 7,
      fill: new Fill({
        color: '#ffcc33',
      }),
    }),
  })
  this.startDraw = function(drawType,callback,params){
    this.init()
    this.draw  = new Draw({
      source:this.vectorSource,
      type: drawType
    })
    if(params.style){
      this.style=params.style;
      if(this.vectorLayer){
        this.vectorLayer.setStyle(this.style)
      }
    }
    this.draw.on('drawend', (e=>{
      var graphical = e.feature.getGeometry()
      var format = new FormatWKT()
      var wkt = format.writeGeometry(graphical)
      if (callback && typeof callback === 'function') {
        callback(wkt)
      }
    }) )
    this.map.addInteraction(this.draw)
  }
  this.stopDraw = function( isClear){
    if( this.draw){
      this.draw.finishDrawing()
      this.map.removeInteraction(this.draw)
      if( isClear ){
        this.vectorSource.clear()
      }
    }
  }
  this.init = function(isClear = true){
    if( this.vectorSource == null ){
      this.vectorSource = new VectorSource()
    }
    if(this.vectorLayer == null){
      this.vectorLayer = new VectorLayer({
        source: this.vectorSource,
        zIndex:100000,
        style: this.style,
      })
      this.map.addLayer(this.vectorLayer)
    }
    if( isClear ){
      this.vectorSource.clear()
    }
  }
  this.addFeature2Layer = function(wktGeometry ,id,isCenterAnimate){
    this.init(false)
    var format = new FormatWKT()
    var feature = format.readFeature(wktGeometry)
    var geom = feature.getGeometry()
    feature.setGeometry(geom)
    feature.setId(id)
    this.vectorSource.addFeature(feature)
    if (isCenterAnimate) {
      if( wktGeometry.indexOf('OINT') > 0 ){
        this.map.getView().animate({ center: geom.getLastCoordinate() }, { duration: 1000 }, { zoom: 18 })
      }else{
        this.map.getView().fit(this.vectorSource.getExtent(), this.map.getSize())
      }
    }
    return feature
  }

  this.removeFeature = function(id){
    this.init(false)
    let feature = this.vectorSource.getFeatureById(id)
    if(feature){
      this.vectorSource.removeFeature(feature)
    }
  }
}

export const DrawType = Object.freeze({
  Point: 'Point',
  LineString: 'LineString',
  Polygon: 'Polygon',
  Circle: 'Circle'
})
