import Feature from 'ol/Feature';
import Point from 'ol/geom/Point';
import {Vector as VectorSource} from 'ol/source'
import {Vector as VectorLayer} from 'ol/layer';
import Select from 'ol/interaction/Select';
import {click, pointerMove} from 'ol/events/condition';
import SkywayStyle from './SkywayStyle'
import LineString from "ol/geom/LineString";
import {PopularScienceType, PathType, PointType, VenueType} from './PopularScienceTypeDefined';

import {
  Text,
  Icon,
  Fill,
  Stroke,
  Style,
  Circle as CircleStyle,
} from "ol/style";
import {WKT as FormatWKT} from "ol/format";

import {Circle} from "ol/geom";

/**
 * 科普宣教-点位通用图层
 */
export default class PoiLayer {
  constructor(map, params) {
    this.map = map;
    this.params = params
    this.dataList = params.dataList || [];
    this.callback = params.callback || function () {
    };
    this.features = new Array();
    this.SkywayStyle = new SkywayStyle(params);
    this.currentSelectFeature = null;

    if (params.hasOwnProperty('isAddPointMoveStyle')) {
      this.isAddPointMoveStyle = params.isAddPointMoveStyle
    } else {
      this.isAddPointMoveStyle = true;
    }
    if (params.hasOwnProperty('isAddSelectEvent')) {
      this.isAddSelectEvent = params.isAddSelectEvent
    } else {
      this.isAddSelectEvent = true;
    }

    this.params = params;

    this.normalStyleList = [];
    this.hoverStyleList = [];


    //将数据进行初始化
    this.source = new VectorSource({projection: 'EPSG:4326', features: this.features});

    let that = this;

    this.vectorLayer = new VectorLayer({
      source: this.source,
      maxZoom: 18,
      style: feature => {
        return this.normalStyle(feature, that)
      },
      zIndex: 100003
    });

    this.selectPointerMove = new Select({
      condition: pointerMove,
      layers: [this.vectorLayer],
      style: feature => {
        return this.hoverStyle(feature, that)
      },
    });
    this.selectClick = new Select({
      condition: click,
      layers: [this.vectorLayer],
      style: feature => {
        return this.hoverStyle(feature, that)
      },
    });
    if (this.isAddPointMoveStyle) {
      this.map.addInteraction(this.selectPointerMove)
    }
    if (this.isAddSelectEvent) {
      this.map.addInteraction(this.selectClick)
    }
    this.selectClick.on("select", function (e) {
      if (e.selected.length > 0) {
        //此时，构建出缓冲区和承灾体
        that.callback(e.selected[0]);
      } else {
        that.callback(null);
      }
    })
    this.currentLayer = this.vectorLayer;
    this.map.addLayer(this.currentLayer);

    this.lineNodeLayer = new VectorLayer({
      source: new VectorSource(),
      projection: 'EPSG:4326', features: [],
      style: new Style({
        fill: new Fill({
          color: 'rgba(255, 255, 255, 0.2)',
        }),
        stroke: new Stroke({
          color: '#ffcc33',
          width: 2,
        }),
        image: new CircleStyle({
          radius: 7,
          fill: new Fill({
            color: '#ffffff',
          }),
          stroke: new Stroke({
            color: '#808080',
            width: 1,
          }),
        }),
      }),
      zIndex: 100004
    });
    this.map.addLayer(this.lineNodeLayer);


    this.refreshFeature(params.dataList);
    this.loadMapEvent();

  }

  triggerSelectFeature() {
    this.selectClick.getFeatures().clear(); //因为我的需求是单选,所以先清除所有选择
    this.selectClick.getFeatures().push(this.currentSelectFeature);//将要选择的要素push进去
    this.selectClick.dispatchEvent({type: "select", selected: [this.currentSelectFeature]})//以上并不会触发'select'事件,所以需要手动触发一下
  }

  removeSelectFeature() {
    this.selectClick.getFeatures().clear();
  }

  normalStyle(feature, that) {
    //判断是点还是线
    let featureType = feature.get("props").typeValue || feature.get("props").type;
    //判断是哪种点
    let pointType = feature.get("props").scienceCategoryValue || feature.get("props").scienceCategory;
    let markerName = this.getMarkNameByType(featureType, pointType);
    let name = markerName;
    if (pointType === PopularScienceType.PATH) {
      name = markerName.key;
    }
    let getStyle = this.getStyleByName(this.normalStyleList, name);
    if (getStyle) {
      return getStyle;
    }
    let style = that.SkywayStyle.setNormalStyle(markerName, featureType);
    this.normalStyleList.push({
      name: name,
      style: style
    })
    return style;
  }

  getStyleByName(list, name) {
    let style = null;
    list.map(item => {
      if (item.name == name) {
        style = item.style;
      }
    })
    return style;
  }

  getMarkNameByType(featureType, pointType) {
    if (featureType === PopularScienceType.PATH) {
      //此时为宣教路径。现状要素。
      //既要渲染线，也要渲染点。需要一个折中的方式进行渲染
      switch (pointType) {
        case PathType.ZRJYLJ:
          //自然教育路径
          return {key: featureType, color: '#bdffc3'};
        case PathType.SLTYLJ:
          //森林体验路径
          return {key: featureType, color: '#fff400'};
        case PathType.ZWJYLJ:
          //植物教育路径
          return {key: featureType, color: '#00ffde'};
        case PathType.DWJYLJ:
          //动物教育路径
          return {key: featureType, color: '#ff8100'};
        default:
          return {};
      }

    } else if (featureType === PopularScienceType.VENUE) {
      //此时为宣教馆
      switch (pointType) {
        case VenueType.BHZHQ:
          //保护综合区
          return 'xjg';

        default:
          return 'xjg';
      }
    } else {
      //此时为宣教点
      switch (pointType) {
        case PointType.DZL:
          //地质类
          return 'dzl';
        case PointType.ZWL:
          //植物类
          return 'zwl';
        case PointType.DWL:
          //动物类
          return 'dwl';
        case PointType.QXL:
          //气象类
          return 'qxl';
        case PointType.ZJL:
          //真菌类
          return 'zjl';
        case PointType.KCL:
          //昆虫类
          return 'kcl';
        default:
          return '';
      }
    }

  }

  hoverStyle(feature, that) {
    //判断是点还是线
    let featureType = feature.get("props").typeValue || feature.get("props").type;
    //判断是哪种点
    let pointType = feature.get("props").scienceCategoryValue || feature.get("props").scienceCategory;
    let markerName = this.getMarkNameByType(featureType, pointType);
    let name = markerName;
    if (pointType === PopularScienceType.PATH) {
      name = markerName.key;
    }
    let getStyle = this.getStyleByName(this.hoverStyleList, name);
    if (getStyle) {
      return getStyle;
    }
    let style = that.SkywayStyle.setNormalStyleScale(markerName, featureType);
    this.hoverStyleList.push({
      name: markerName,
      style: style
    })
    return style;
  }

  loadMapEvent() {
    let map = this.map;
    map.on('pointermove', function (e) {
      var pixel = map.getEventPixel(e.originalEvent);
      var feature = map.forEachFeatureAtPixel(pixel, function (feature) {
        if (feature) {
          let geom = feature.getGeometry();
          if (geom instanceof Point) {
            return feature;
          } else {
            return null;
          }
        }
        return feature;
      })
      if (feature == undefined) {
        map.getTargetElement().style.cursor = "auto"
      } else {
        map.getTargetElement().style.cursor = "pointer"
      }
    })
    /*let _this=this;
    this.map.on('singleclick',function(evt){
      var feature = this.forEachFeatureAtPixel(evt.pixel, function(feature, layer) {
        return feature;
      });
      _this.callback(feature)

    })*/
  }


  hide() {
    this.map.removeLayer(this.currentLayer);
  }

  show() {
    this.map.removeLayer(this.currentLayer);
    this.map.addLayer(this.currentLayer);
  }

  refreshFeature(dataList) {
    this.features = [];
    this.dataList = dataList;
    if (!dataList) {
      this.dataList = [];
    }
    this.source.clear();
    this.lineNodeLayer.getSource().clear();

    for (var i = 0; i < this.dataList.length; ++i) {

      let data = this.dataList[i];
      let meType = data.typeValue || data.type;
      if (meType == PopularScienceType.PATH) {
        //此时为宣教路径，需要进行额外的数据处理。
        let lineDataList = data.list || [];
        let lineCoordinates = lineDataList.map(item => {
          let coordinate = [item.longitude * 1, item.latitude * 1]
          let feature = new Feature(new Point(coordinate));
          this.lineNodeLayer.getSource().addFeature(feature);
          return coordinate;
        });
        let lineFeature = new Feature(new LineString(lineCoordinates));
        lineFeature.set('props', data);
        this.features.push(lineFeature);
      } else {
        let x = this.dataList[i].longitude - 0
        let y = this.dataList[i].latitude - 0
        var coordinates = [x, y];
        if (x > 136 || x < 73 || y > 54 || y < 3) {
          continue
        }
        let feature = new Feature(new Point(coordinates));
        feature.set("props", dataList[i]);

        this.features.push(feature)
      }


    }
    this.source.addFeatures(this.features);

  }

  selectFeatureById(id) {
    this.features.map(item => {
      let props = item.get('props');
      if (props.uuid == id) {
        this.currentSelectFeature = item;
      }
    })
    this.selectClick.getFeatures().clear(); //因为我的需求是单选,所以先清除所有选择
    this.selectClick.getFeatures().push(this.currentSelectFeature);//将要选择的要素push进去
    this.selectClick.dispatchEvent({type: "select", selected: [this.currentSelectFeature]})
  }

  zoomToExtent() {
    this.map
        .getView()
        .fit(this.currentLayer.getSource().getExtent(), {
          size: this.map.getSize(),
          padding: [50, 50, 50, 50],
          constrainResolution: true,
          duration: 500,
        });
  }

}
