<template>
  <div class="main1" :style="getSymbolStyle()">
    <div class="query_box">
      <el-input
        v-model="queryParams.name"
        class="query_input"
        placeholder="请输入地名"
        clearable
        @change="onInput"
        @input="onInput"
      >
        <el-button
          slot="append"
          class="query_button"
          icon="el-icon-search"
          @click="init()"
        />
      </el-input>
    </div>
    <div
      v-if="queryParams.name.length !== 0"
      class="query_result"
      @mouseenter="enter()"
      @mouseleave="leave()"
    >
      <div class="result-container">
        共找到{{ total }}条结果
        <span class="switchSpam" style="display: none" @click="switchWay()">{{
          isBaiDu ? "天地图" : "百度"
        }}</span>
      </div>
      <div v-if="tableShow && total > 0">
        <!--表格渲染-->
        <el-table
          v-loading="loading"
          :data="data"
          :row-class-name="tableRowClass"
          size="small"
          style="width: 100%"
          @row-click="location2Map"
        >
          <el-table-column>
            <template slot-scope="scope">
              <div style="color: #fff; font-weight: bold">
                {{ scope.$index + 1 }}.{{ scope.row.name }}
              </div>
              <div style="text-indent: 10px">
                <span style="color: #fff"></span>{{ scope.row.description }}
              </div>
            </template>
          </el-table-column>
          <el-table-column width="80" align="center">
            <template slot-scope="scope">
              <div>
                <svg-icon
                  icon-class="icon_poi_loc"
                  style="font-size: 10px; color: #666666"
                />
              </div>
            </template>
          </el-table-column>
        </el-table>
        <!--分页组件-->
        <div class="pageBox">
          <el-pagination
            :pager-count="5"
            class="pad-test"
            style="margin-top: 4px"
            layout="prev, pager, next"
            v-show="total > 0"
            :total="total"
            :page.sync="queryParams.pageNum"
            :limit.sync="queryParams.pageSize"
            @pagination="init()"
          />
        </div>
        <div
          style="
            width: 100%;
            height: 30px;
            line-heihgt: 30px;
            text-align: center;
            font-size: 14px;
            color: #fff;
            cursor: pointer;
            display: none;
          "
          @click="init()"
        >
          更多
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import { listPoi } from "@/api/gis/poi";

export default {
  name: "PoiQuery",
  props: {
    defaultRigth: {
      type: Number,
      default: 20,
      require: false,
    },
  },
  data: function () {
    return {
      loading: false,
      expand: true,
      queryType: "2",
      extent: [],
      tableShow: true,
      isBaiDu: false,
      total: 0,
      queryParams: {
        pageNum: 1,
        pageSize: 10,
        name: "",
      },
      data: [],
    };
  },
  watch: {},
  created() {
    this.$nextTick(() => {});
  },
  methods: {
    init() {
      this.loading = true;
      listPoi(this.queryParams).then((response) => {
        this.data = response.rows;
        this.total = response.total;
        this.loading = false;
        console.log("total = " + this.total);
      });
    },
    onInput(value) {
      this.init();
    },
    location2Map(row) {
      this.$parent.location("POINT(" + row.lon + " " + row.lat + ")", true);
    },
    enter() {
      this.tableShow = true;
    },
    leave() {
      this.tableShow = false;
    },
    tableRowClass(row) {
      return "rowClassName";
    },
    refresh() {
      if (
        this.queryParams.name === undefined ||
        this.queryParams.name.length === 0
      )
        return;
      if (this.isBaiDu) {
        this.initBaiDu();
      } else {
        this.init();
      }
    },
    switchWay() {
      if (this.isBaiDu) {
        this.isBaiDu = false;
      } else {
        this.isBaiDu = true;
      }
      this.refresh();
    },
    expandMapComponent(isExpand) {
      this.expand = isExpand;
    },
    getSymbolStyle() {
      if (!this.expand) {
        return "transform: translateX(" + -this.defaultRigth + "vw)";
      }
    },
    onExpandClick() {
      this.expand = !this.expand;
      this.$emit("onExpandClick", this.expand);
    },
  },
};
</script>

