<template>
  <div class="mini_map">
    <div :id="mapId" class="mini_map map"/>
    <span class="layerName">{{layer.name}}</span>
  </div>

</template>
<script>
import "ol/ol.css";
import Map from "ol/Map";
import View from "ol/View";
import OlLayerManager from '../lib/OlLayerManager'

export default {
  name: 'MiniMap',
  props:{
    mapId:{
      type: String,
      default: 'mapMin',
    },
    index:{
      type: Number,
      require: true
    },
    layer:{
      type:Object,
      require:true
    }
  },
  data(){
    return {
      map: null,
      layerManager: null,
      zoom: 12,
      center: [106.33049, 24.81812]
    }
  },
  mounted() {
    this.initMap()
  },
  methods:{
    initMap(){
      this.view = new View({
        projection: "EPSG:4326",
        center: this.center,
        maxZoom: 17,
        minZoom: 0,
        zoom: this.zoom,
      });
      this.map = new Map({
        target: this.mapId,
        view: this.view,
        maxZoom: 17,
      });
      this.layerManager = new OlLayerManager(this.map);
      this.layer.layerName = this.layer.name
      // let configLayerTDTIMAGEW = {
      //   'type': '1',
      //   'layerType': '1',
      //   'layerIndex': 1,
      //   'layerName': '天地图_影像底图',
      //   'url': 'img_w'
      // }
      this.layerManager.addConfigLayer(this.layer, false)
      // this.$parent.addMap(this,this.index)
      this.registerAction()
      this.$emit('onMapInit',this,this.index)
    },
    updateLayer(updatelayer){
      this.layey = updatelayer
      this.map.getLayers().forEach(layer=> {
        this.map.removeLayer(layer);
      })
      this.layer.layerName = this.layer.name
      this.layerManager.addConfigLayer(this.layey, false)
    },
    clear(){
      // this.map.getLayers().forEach(layer=> {
      //   this.map.removeLayer(layer);
      // })
    },
    registerAction(){
      this.map.on("pointerdrag",(e)=>{
        // 拖动事件
        this.zoom = this.map.getView().getZoom()
        this.center = this.map.getView().getCenter()
        // this.$parent.syncMapAction(this.zoom , this.center)
        this.$emit('syncMapAction',this.zoom , this.center)
      })
      this.map.on("moveend",(e)=>{
        // 拖动事件
        this.zoom = this.map.getView().getZoom()
        this.center = this.map.getView().getCenter()
        // this.$parent.syncMapAction(this.zoom , this.center)
        this.$emit('syncMapAction',this.zoom , this.center)
      })
    },

    syncMapAction(center,zoom){
      if( zoom == this.zoom && center[0] === this.center[0] && center[1] == center[1]) return
      console.log(this.layer.layerName)
      this.zoom = zoom
      this.center = center
      this.map.getView().setCenter(center)
      this.map.getView().setZoom(zoom)
    }
  }
}
</script>

<style lang="scss" >
 .mini_map{
 }
 .mini_map .ol-zoom {
  display: none;
}
</style>
<style lang="scss" scoped>
 .mini_map {
    width: 100%;
    height: 100%;
    position: relative;
  }
 .map{
   position: absolute;
   z-index: 1;
 }
 .layerName{
   position: absolute;
   z-index: 2;
   left: 16px;
   top: 16px;
   color:red;
   font-size: large;
 }
</style>
