<template>
  <div class="skymap" :id="mapId + '_skyway'">
    <div class="skymap" :id="mapId + '_skymap'">
      <div :id="mapId" ref="skymap" class="map" />
    </div>
    <div id="popup" ref="popup" class="ol-popup" v-if="isPopuWindowVisible">
      <a id="popup-closer" href="#" class="ol-popup-closer" />
      <div id="popup-content">
        <case-msg ref="caseMsg" @msgEvent="popupEmitEvent" ></case-msg>
      </div>
    </div>

    <map-tool-bar
      ref="mapTooBar"
      class="map_tool_bar"
      @onExpandClick="onExpand"
      @showLayer="showLayerManager"
      v-show="isShowMapToolBar && !isDrawMode"
      :isSjjc="isSjjc"
      :style="getSymbolStyle()"
    />
    <layer-manager ref="mapLayerManager" class="map_layer" />
    <map-query-result
      ref="mapQueryResult"
      class="map_query_result"
      :style="getSymbolStyle()"
    />
    <poi-query
      ref="poiQuery"
      class="poiQuery"
      :style="getSymbolStyle()"
      v-show="isShowPoiQuery && !isDrawMode"
    />

    <land-mark-query
      ref="poiQuery"
      class="poiQuery"
      :style="getSymbolStyle()"
      v-show="isShowlandMarkQuery && !isDrawMode"
    />

    <layer-symbol
      ref="layerSymbol"
      :class="layerSymbolClass"
      :style="getSymbolStyle()"
      :defaultRight="defaultRight"
      v-show="isShowLayerSymbol "
    />
    <el-button
      class="filter-item buttonDraw"
      v-show="isShowDraw"
      size="mini"
      type="primary"
      @click="handleDraw"
      >绘制</el-button
    >
    <div class="compass" v-show="subjecMapModel"></div>
  </div>
</template>

<script>
import "ol/ol.css";
import Map from "ol/Map";
import View from "ol/View";
import { ScaleLine, FullScreen } from "ol/control";
import OlLayerManager from "./lib/OlLayerManager";
import LayerSymbol from "./component/LayerSymbol";
import MapToolBar from "./component/MapToolBar";
import LayerManager from "./component/LayerManager";
import MapQueryResult from "./component/MapQueryResult";
import PoiQuery from "./component/PoiQuery";
import OlMapExport from "./lib/OlMapExport";
import OlDraw from "./lib/OlDraw";
import landMarkQuery from "./component/landMarkQuery";
import { transform } from "ol/proj";
import Overlay from "ol/Overlay";

import caseMsg from "./component/caseMsg";

/**
 * 抽离出来的地图组件，摒弃掉原先广西项目中的多余内容。
 */
export default {
  name: "SkyMapCase",
  components: {
    LayerSymbol,
    MapToolBar,
    LayerManager,
    MapQueryResult,
    PoiQuery,
    landMarkQuery,
    caseMsg,
  },
  data() {
    return {
      map: null,
      layerManager: null,
      mapExport: null,
      mapExtent: null,
      olDraw: null,
      zoom: 12,
      center: [106.33049, 24.81812], // 雅长
      // center: [111.73576, 24.10400], // 大桂山
      contractFlag: true,
      yearFlag: true,
      // isPopuWindowVisible: false,
      isOpen: false,
      isShowlandMarkQuery: false,
      mapScale: null,
      drawStyle:null,//标绘时的要素样式
      fullScreen: null,
      // map_params:{"zoom":12,"center":[111.74843,24.08303],"maxZoom":18,"minZoom":12,"extent":[111.4691,23.9376,112.0709,24.2620]}, // 大桂山
      map_params:{"zoom":12,"center":[106.33049,24.81812],"maxZoom":18,"minZoom":12,"extent":[106.0538,24.6709,106.6306,24.9818]}, // 雅长
      /*map_params: {
        zoom: 12,
        center: [110.309, 26.3117],
        maxZoom: 18,
        minZoom: 12,
        extent: [109.211, 25.476, 111.313, 27.023],
      }, // 南山*/
      // map_params:{"zoom":12,"center":[111.11733,30.42440],"maxZoom":18,"minZoom":6,"extent":[110.9795,30.3083,111.2742,30.4927]} // 长阳清江
    };
  },
  props: {
    expand: {
      type: Boolean,
      default: true,
    },
    layerSymbolClass:{
      type:String,
      default:'layer_symbol'
    },
    isPopuWindowVisible: {
      type: Boolean,
      default: false,
    },

    isShowDraw: {
      type: Boolean,
      default: false,
    },
    mapId: {
      type: String,
      default: "map",
    },
    popupId: {
      type: String,
      default: "popup",
    },
    // 组件右边距
    defaultRight: {
      type: Number,
      default: 21.19,
      require: false,
    },
    isShowMapToolBar: {
      type: Boolean,
      default: true,
    },
    isShowLayerSymbol: {
      type: Boolean,
      default: true,
    },
    isShowPoiQuery: {
      type: Boolean,
      default: false,
    },
    isDrawMode: {
      type: Boolean,
      default: false,
    },
    drawGraphicsType: {
      type: String,
      default: "4",
    },
    subjecMapModel: {
      // 专题图地图
      type: Boolean,
      require: false,
      default: false,
    },
    isSjjc: {
      type: Boolean,
      default: false,
    },
    isShowScale: {
      type: Boolean,
      default: true,
    },
    initMapParams: {
      type: String,
      default: null,
    },
    defaultFeatureQuery: {
      // 是否默认开启查询
      type: Boolean,
      default: false,
    },
    componentZoom: {
      type: Number,
      require: false,
    },
  },
  mounted() {
    /*this.getDicts("map_params").then((res) => {
      if (res.code === 200) {
        if (res.data.length > 0) {
          if (this.initMapParams) {
            //判断显示那个项目
            for (let i = 0; i < res.data.length; i++) {
              if (res.data[i].dictLabel == this.initMapParams) {
                this.map_params = JSON.parse(res.data[i].dictValue);
                break;
              }
            }
          } else {
            this.map_params = JSON.parse(res.data[0].dictValue);
          }
        }
        this.initMap();
      }
    });*/
    this.initMap();
  },
  methods: {
    layerShow() {
      this.layerFlag = !this.layerFlag;
    },
    onExpand(expand) {
      this.contractFlag = expand;
      this.$emit("contract", expand);
      this.expandMapComponent(expand);
    },
    initMap() {

      this.view = new View({
        projection: "EPSG:4326",
        center: this.map_params.center,
        maxZoom: this.map_params.maxZoom,
        minZoom: this.map_params.minZoom,
        zoom: this.componentZoom ? this.componentZoom : this.map_params.zoom,
        extent: this.map_params.extent,
      });
      this.map = new Map({
        target: this.mapId,
        view: this.view,
      });
      var _this = this;
      this.layerManager = new OlLayerManager(
        this.map,
        this.onFeatureSelected,
        this.initMapParams,
        {
          mapChanged: () => {
            _this.refreshSymbols(_this.layerManager.getCurrentConfigLayers());
          },
        }
      );
      this.mapExport = new OlMapExport(this.map);
      // 大屏地图
      if (this.isDrawMode) {
        // 绘制定位地图
        this.layerManager.initMiniMap();
        this.olDraw = new OlDraw(this.map);
      } else {
        this.$refs.mapTooBar.initMap(this.map, this.layerManager);
        this.$refs.mapLayerManager.initMap(this.map, this.layerManager);
        this.$refs.mapLayerManager.initMapLayer();
      }
      if (this.isShowScale) {
        //  添加地图比例尺
        this.mapScale = new ScaleLine({
          // bar: true,
          // text: true,
          units: "metric",
          // minWidth: 125,
          className: "mapScale",
        });
        this.map.addControl(this.mapScale);
      }
      if (this.defaultFeatureQuery) {
        this.initFeatureSelect();
      }

      this.expandMapComponent(this.expand);
      this.$emit("mapComplete", this.map);
    },




    refreshPopContentById(id) {
      this.$refs.wzmsg.init(id);
    },
    popuWindowVisible(visible) {
      this.isPopuWindowVisible = visible;
    },
    getSymbolStyle() {
      return "right: " + this.defaultRight + "vw;";
    },
    expandMapComponent(isExpand) {
      this.expand = isExpand;
      this.$refs.layerSymbol.expandMapComponent(isExpand);
      this.$refs.mapLayerManager.expandMapComponent(isExpand);
      this.$refs.mapQueryResult.expandMapComponent(isExpand);
      this.$refs.poiQuery.expandMapComponent(isExpand);
      this.$refs.mapTooBar.expandMapComponent(isExpand);
      this.scaleLocation();
    },
    scaleLocation() {
      if (this.isShowScale) {
        if (!this.expand) {
          // 展开
          this.mapScale.element.className = "mapScale rigthExpand"; // 0
        } else {
          // 非展开
          this.mapScale.element.className = "mapScale rigthUnExpand"; // 21
          this.mapScale.element.style.right = this.defaultRight+0.5 + "vw";
        }
      }
    },
    showLayerManager() {
      this.$refs.mapLayerManager.showLayerManager();
    },
    /**
     * 图例展示
     * @param symbols
     */
    showSymbols(symbols) {
      this.$refs.layerSymbol.showSymbols(symbols);
    },
    /**
     * 清空图例
     * @param symbols
     */
    clearSymbols() {
      this.$refs.layerSymbol.clearAll();
    },
    /**
     * 查询结果展示
     * @param features
     */
    showFeatureList(features) {
      this.$refs.mapQueryResult.showFeature(features);
      features.forEach((feature) => {
        this.location(feature.wkt, false);
      });
    },
    location(wkt, isClear) {
      this.layerManager.addSelectFeature(wkt, isClear);
      this.layerManager.setFullExtent(wkt);
    },
    draw(drawType, callback) {
      if (this.olDraw) {
        this.olDraw.startDraw(drawType, callback);
      }
    },
    /**
     * 全图
     */
    zoom2extent() {
      this.map.getView().animate({
        center: this.center,
        zoom: this.zoom,
        duration: 0,
      });
    },
    handleDraw() {
      if (this.drawGraphicsType === "4") return;
      this.olDraw.stopDraw(true);
      let drawType = "";
      if (this.drawGraphicsType === "1") {
        drawType = "Point";
      } else if (this.drawGraphicsType === "2") {
        drawType = "LineString";
      } else if (this.drawGraphicsType === "3") {
        drawType = "Polygon";
      }
      if (this.olDraw) {
        this.olDraw.startDraw(drawType, (wkt) => {
          this.$emit("onDrawFinish", wkt);
          this.olDraw.stopDraw(false);
          this.drawStyle=null;
        },{style:this.drawStyle});
      }
    },
    setDrawStyle(style){
      this.drawStyle=style
    },
    clearDraw() {
      this.olDraw.stopDraw(true);
    },
    reSize() {
      var _this = this;
      setTimeout(function () {
        _this.map.updateSize();
      }, 200);
    },
    /**
     * 要素选中事件回调
     * @param layerNames
     * @param isChecked
     */
    onFeatureSelected(configLayer, features, isSelected) {
      if (isSelected) {
        this.$emit("featureSelected", configLayer, features, true);
      } else {
        this.$emit("featureSelected", null, null, false);
      }
    },
    /**
     * 设置图层选中加载 或 取消加载
     * @param layerNames
     * @param isChecked
     */
    setLayerSelect(selectLayers, isChecked) {
      this.$refs.mapLayerManager.setLayerSelect(selectLayers, isChecked, false);
    },
    /**
     * 设置图层高亮
     * @param layerNames 图层名称
     * @param highLightTypeName wms cql 暂支持
     */
    selectLayerHighLight(selectLayers) {
      this.$refs.mapLayerManager.setLayerSelect(selectLayers, false, true);
    },
    /**
     * 服务要素点击事件
     */
    initFeatureSelect() {
      var _this = this;
      this.map.on("singleclick", function (evt) {
        _this.layerManager.querySelectFeature(evt, (configLayer, features) => {
          if (configLayer && features && features.length > 0) {
            let id = features[0].properties.id;
            _this.selectLayerHighLight(
              [configLayer.layerName],
              "where id = '" + id + "'"
            );
            _this.onFeatureSelected(configLayer, features, true);
          } else {
            _this.layerManager.clearLastHighLightConfigLayer();
            _this.onFeatureSelected(null, null, false);
          }
        });
      });
    },
    /**
     * 刷新图例
     * @param configLayers
     */
    refreshSymbols(configLayers) {
      this.$refs.layerSymbol.refreshSymbols(configLayers);
    },
    /**
     * 根据图层名称获取图例
     * @param layerName
     * @returns
     */
    getSymbolsByLayerName(layerName) {
      return this.$refs.mapLayerManager.getSymbolsByLayerName(layerName);
    },
    /**
     * 地图缩方至指定范围
     * @param extent
     */
    zoomMap2extent(extent) {
      this.map
        .getView()
        .fit(extent, {
          size: this.map.getSize(),
          padding: [50, 50, 50, 50],
          constrainResolution: true,
          duration: 500,
        });
    },

    //初始化地图弹框
    initPopup(callback) {
      const container = document.getElementById("popup");
      const closer = document.getElementById("popup-closer");
      this.overlay = new Overlay({
        element: container,
        autoPan: true,
        autoPanAnimation: {
          duration: 250,
        },
        offset: [0, -40],
      });
      closer.onclick = () => {
        this.overlay.setPosition(undefined);
        closer.blur();
        callback();
        return false;
      };

      this.map.addOverlay(this.overlay);
    },

    popupEmitEvent(params) {
      this.$emit("popupEmitEvent", params);
    },
  },
};
</script>
<style lang="scss" scoped>
</style>
