    background: url("./img/bilichi.png") no-repeat;
    background-size: 100% 8px;
    background-position-y: bottom;
    color: #ffffff;
    font-size: 5px;
    text-align: center;
    margin: 1px;
    will-change: contents, width;
    transition: all 0.25s;
    height: 28px;
    line-height: 20px;
  }
  .rigthExpand {
    right: 1vw;
  }
  .rigthUnExpand {
    right: 21.9vw;
  }
}
</style>
