<template>
  <div class="ContentRight">
    <div class="ItemBox">
      <div class="appHomeTitBox">
        <div class="titleName">图片/视频资料</div>
      </div>
      <div class="ContentBox">
        <div
          class="AddImg"
          v-for="(item, index) in fileList"
          :key="index"
          :data-name="item.fileName"
          v-if="
            documentExts.findIndex((c) => c == item.fileType.toLowerCase()) ==
            -1
          "
        >
          <div
            class="item img"
            v-if="
              imageExts.findIndex((c) => c == item.fileType.toLowerCase()) != -1
            "
            @click="
              showImage(
                fileList
                  .filter(
                    (c) =>
                      imageExts.findIndex(
                        (b) => b == c.fileType.toLowerCase()
                      ) != -1
                  )
                  .findIndex((c) => c.uuid == item.uuid)
              )
            "
            :style="{
              background: 'url(' + setBackImage(item.filePath) + ')',
            }"
          ></div>

          <div
            class="video"
            v-else-if="
              videoExts.findIndex((c) => c == item.fileType.toLowerCase()) != -1
            "
            @click="showVideo(item.filePath, index)"
          >
            <div
              class="videoIcon"
              :style="{
                background:
                  'url(' +
                  (videoActionIndex == index ? playerSta : playerEnd) +
                  ')',
              }"
            ></div>
          </div>
        </div>
      </div>
    </div>
    <div class="ItemBox">
      <div class="appHomeTitBox">
        <div class="titleName">文档资料</div>
      </div>
      <div class="ContentBox box2" id="box2">
        <div
          class="AddImg"
          v-for="(item, index) in fileList"
          :key="index"
          v-if="
            documentExts.findIndex((c) => c == item.fileType.toLowerCase()) !=
            -1
          "
          :data-name="item.fileName"
          @click="getDocument(item.filePath, item.fileName)"
        >
          <img
            class="item img"
            :src="
              item.fileType.toLowerCase() == 'pdf'
                ? require('@/assets/phone/APP_detail/RDF.png')
                : require('@/assets/phone/APP_detail/wendang.png')
            "
            alt
          />
        </div>
      </div>
    </div>
    <van-overlay :show="show" @click="show = false">
      <div class="wrapper">
        <video
          id="myVideo"
          class="video-js vjs-default-skin vjs-big-play-centered videoItem"
          controls
          autoplay="false"
          preload="auto"
          poster=""
        >
          <!-- <source :src="item.filePath" type="video/mp4" /> -->
        </video>
      </div>
    </van-overlay>
  </div>
</template>

<script>
import { ImagePreview, Overlay, Icon } from "vant";
export default {
  components: {
    "van-overlay": Overlay,
    "van-icon": Icon,
  },
  data() {
    return {
      show: false,
      fileList: [],
      imageExts: ["png", "jpg", "jpeg", "gif", "bmp"],
      videoExts: ["mp4", "avi"],
      documentExts: [
        "docx",
        "doc",
        "pptx",
        "ppt",
        "pdf",
        "xml",
        "html",
        "txt",
        "xlsx",
        "xls",
      ],
      myPlayerOptions: {
        bigPlayButton: true, // 是否显示播放按钮（这个播放按钮默认会再左上方，需用CSS设置居中）
        textTrackDisplay: true,
        posterImage: true,
        errorDisplay: true,
        controlBar: false,
        autoplay: false, // 不设置自动播放，若未true，则无法看到视频海报图片
        controls: true,
        notSupportedMessage: "此视频暂无法播放，请稍后再试",
      },
      playerEnd: require("@/assets/phone/popularization/stopPlay.png"),
      playerSta: require("@/assets/phone/popularization/play.png"),
      videoActionIndex: "",
      myPlayer: null,
    };
  },
  watch: {
    show(val) {
      if (!val) {
        this.videoActionIndex = "";
        if (this.myPlayer) {
          this.myPlayer.pause();
        }
      }
    },
  },
  methods: {
    setBackImage(filePath) {
      return process.env.VUE_APP_ACCESSORY_URL + filePath;
    },
    getDocument(url, filename) {
      window.location.href = process.env.VUE_APP_ACCESSORY_URL + url;
      // let eleLink = document.createElement("a");
      // eleLink.download = filename;
      // eleLink.style.display = "none";
      // eleLink.href = url;
      // document.body.appendChild(eleLink);
      // eleLink.click();
      // URL.revokeObjectURL(eleLink.href);
      // document.body.removeChild(eleLink);
    },
    showVideo(path, index) {
      this.videoActionIndex = index;
      this.show = true;
      this.myPlayer = this.$video("myVideo"); // 初始化视频
      this.myPlayer.src(process.env.VUE_APP_ACCESSORY_URL + path); //重置video的src
      this.myPlayer.load(process.env.VUE_APP_ACCESSORY_URL + path); //使video重新加载
    },
    showImage(index) {
      let imgs = this.fileList
        .filter(
          (c) =>
            this.imageExts.findIndex((d) => d == c.fileType.toLowerCase()) != -1
        )
        .map((c) => {
          return process.env.VUE_APP_ACCESSORY_URL + c.filePath;
        });
      ImagePreview({
        images: imgs,
        startPosition: index,
        onClose() {
          // do something
        },
      });
    },
  },
  created() {
    console.log(1);
    this.fileList = JSON.parse(
      this.$store.state.activityView.activity
    ).fileList;
    console.log(this.fileList);
  },
  mounted() {},
  beforeDestroy() {
    if (this.myPlayer) {
      this.myPlayer.dispose();
    }
  },
};
</script>
<style lang='scss' scoped>
.wrapper {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
  .videoItem {
    height: 50vh;
    width: 90vw;
  }
}

.ContentRight {
  width: 100vw;
  .ItemBox {
    background-color: white;
    width: 95vw;
    margin: 0 auto;
    padding: 0 2vw;
    .appHomeTitBox {
      height: 6.55vw;
      line-height: 6.55vw;
      font-size: 4vw;
      margin: 0vw 0 0 1vw;
      padding-top: 3vw;
      display: flex;
      justify-content: space-between;
      .titleName {
        font-size: 5vw;
        font-weight: 600;
        color: #484848;
        padding-left: 3vw;
        position: relative;
        margin-left: 1vw;
        &::before {
          content: " ";
          position: absolute;
          height: 70%;
          width: 2px;
          top: 15%;
          left: 0;
          background-color: #484848;
        }
      }
    }
    .ContentBox {
      width: 100%;
      height: 45vh;
      padding-top: 3vw;
      overflow-y: auto;

      .AddImg {
        margin: 0 1vw;
        display: inline-block;
        position: relative;

        &::after {
          // content: attr(data-name);
          position: absolute;
          bottom: 0;
          left: 0;
          text-align: center;
          width: 100%;
          white-space: nowrap;
          overflow: hidden;
          text-overflow: ellipsis;
          font-size: 2vw;
          // transform: scale(0.8);
          color: #bf9b6b;
        }
        .item {
          height: 17vw;
          width: 17vw;
        }
        .img {
          background-size: cover !important;
        }
        .video {
          height: 8.5vw;
          width: 8.5vw;
          background: #00000026;
          padding: 4.25vw;
          .videoIcon {
            height: 100%;
            width: 100%;
            background-size: cover !important;
          }
        }
      }
    }
    .box2 {
      height: 30vh;
    }
  }
}
</style>