import request from '@/utils/request'

// 查询地图服务列表
export function listMapservice(query) {
  return request({
    url: '/gis/mapservice/list',
    method: 'get',
    params: query
  })
}
export function listJurisdiction(query) {
  return request({
    url: '/gis/mapservice/listJurisdiction',
    method: 'get',
    params: query
  })
}

// 查询地图服务详细
export function getMapservice(id) {
  return request({
    url: '/gis/mapservice/' + id,
    method: 'get'
  })
}

// 新增地图服务
export function addMapservice(data) {
  return request({
    url: '/gis/mapservice',
    method: 'post',
    data: data
  })
}

// 修改地图服务
export function updateMapservice(data) {
  return request({
    url: '/gis/mapservice',
    method: 'put',
    data: data
  })
}

// 删除地图服务
export function delMapservice(id) {
  return request({
    url: '/gis/mapservice/' + id,
    method: 'delete'
  })
}

// 导出地图服务
export function exportMapservice(query) {
  return request({
    url: '/gis/mapservice/export',
    method: 'get',
    params: query
  })
}


export function queryMulitLayers(query) {
  return request({
    url: '/gis/mapservice/queryMulitLayers',
    method: 'get',
    params: query
  })
}


export function queryFeatureBySql(query) {
  return request({
    url: '/gis/mapservice/queryFeatureBySql',
    method: 'get',
    params: query
  })
}
