<template>
  <div class="ContentLeft">
    <div class="ItemBox">
      <div class="appHomeTitBox">
        <div class="titleName">基本信息</div>
        <div class="GreenName">
          {{ activityInfo.serialNumber }}
        </div>
      </div>
      <div class="appHomeContent">
        <div class="contentItem">
          <div class="itemLabel">活动名称</div>
          <div class="itemValue w77">
            <input type="text" :value="activityInfo.activityName" />
          </div>
        </div>
        <div class="contentItem flex2">
          <div class="itemLabel">活动主题</div>
          <div class="itemValue w77">
            <input type="text" :value="activityInfo.activityTheme" />
          </div>
        </div>
        <div class="contentItem flex2">
          <div class="itemLabel">科普方式</div>
          <div class="itemValue w77">
            <div class="addDate">
              <input type="text" :value="activityInfo.popularMode" />
            </div>
          </div>
        </div>
        <div class="contentItem flex2">
          <div class="itemLabel">科普对象</div>
          <div class="itemValue w77">
            <div class="addDate">
              <input type="text" :value="activityInfo.popularPeople" />
            </div>
          </div>
        </div>
        <div class="contentItem flex2">
          <div class="itemLabel">科普人数</div>
          <div class="itemValue w77">
            <input type="text" :value="activityInfo.popularPeopleNum + '人'" />
          </div>
        </div>
        <div class="contentItem flex2">
          <div class="itemLabel">开始时间</div>
          <div class="itemValue w77">
            <div class="addDate">
              <input type="text" :value="setTime(activityInfo.startTime)" />
            </div>
          </div>
        </div>
        <div class="contentItem flex2">
          <div class="itemLabel">结束时间</div>
          <div class="itemValue w77">
            <div class="addDate">
              <input type="text" :value="setTime(activityInfo.endTime)" />
            </div>
          </div>
        </div>
      </div>
      <div class="appHomeTitBox mTop">
        <div></div>
        <div class="GreenName">
          累计时长 {{ activityInfo.activityDuration * 24 }}小时
        </div>
      </div>
    </div>
    <div class="ItemBox">
      <div class="appHomeTitBox">
        <div class="titleName">基本信息</div>
      </div>
      <div class="appHomeContent">
        <div class="contentItem">
          <div class="itemLabel">路径/范围</div>
          <div class="itemValue w77">
            <div class="addDate">
              <input type="text" :value="setPath(activityInfo.activityPath)" />
            </div>
          </div>
        </div>
        <div class="contentItem">
          <div class="itemLabel">地名</div>
          <div class="itemValue w77">
            <input type="text" :value="activityInfo.placeName" />
          </div>
        </div>
        <div class="contentItem">
          <div class="itemLabel">定位</div>
          <div class="itemValue w77">
            <!-- //latitude -->
            <input
              type="text"
              :value="
                '经度:' +
                Math.round(activityInfo.longitude * 10000) / 10000 +
                '     纬度:' +
                Math.round(activityInfo.latitude * 10000) / 10000
              "
            />
          </div>
        </div>
      </div>
    </div>
    <div class="ItemBox">
      <div class="appHomeTitBox">
        <div class="titleName">宣教路径/范围</div>
      </div>
      <div class="appHomeContent">
        <div class="mapBox">
          <sky-map-case
            class="skymap"
            ref="skymap"
            :isDrawMode="true"
            :isShowDraw="false"
            @mapComplete="mapComplete"
            :is-show-map-tool-bar="false"
            :is-show-bus-layer="false"
            :expand="false"
          ></sky-map-case>
        </div>
      </div>
    </div>
    <div class="ItemBox">
      <div class="appHomeTitBox">
        <div class="titleName">活动管理</div>
      </div>
      <div class="appHomeContent">
        <div class="contentItem">
          <div class="itemLabel">科普资料</div>
          <div class="itemValue w66">
            <div v-for="(item, index) in activityInfo.centreList" :key="index">
              <span>{{ setMaterialType(item.materialId) }}</span>
              <span>{{ item.quantity }} 册</span>
            </div>
          </div>
        </div>
        <div class="contentItem flex2">
          <div class="itemLabel">活动负责人</div>
          <div class="itemValue w77">
            <input type="text" :value="activityInfo.activityLeader" />
          </div>
        </div>
        <div class="contentItem flex2">
          <div class="itemLabel">主办方人员</div>
          <div class="itemValue w77">
            <input type="text" :value="activityInfo.sponsorPersonnel + '人'" />
          </div>
        </div>
        <div class="contentItem flex2">
          <div class="itemLabel">管理单位</div>
          <div class="itemValue w77">
            <input type="text" :value="activityInfo.managementUnit" />
          </div>
        </div>
        <div class="contentItem flex2">
          <div class="itemLabel">费用/预算</div>
          <div class="itemValue w77">
            <input type="text" :value="setCost(activityInfo.cost)" />
          </div>
        </div>
        <div class="contentItem flex2">
          <div class="itemLabel">制表人</div>
          <div class="itemValue w77">
            <input type="text" :value="activityInfo.tabulator" />
          </div>
        </div>
        <div class="contentItem flex2">
          <div class="itemLabel">制表日期</div>
          <div class="itemValue w77">
            <div class="addDate">
              <input
                type="text"
                :value="setTime(activityInfo.tabulationTime)"
              />
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import PoiLayer from "@/views/components/lib/PoiLayer";
import SkyMapCase from "@/views/map/SkyMapCase";
import { activePath, materialist } from "@/api/data/reservation";

export default {
  components: {
    SkyMapCase,
  },
  data() {
    return {
      poiLayer: null,
      activityInfo: {},
      pathList: [], //科普路径
      materialList: [], //科普资料
    };
  },
  created: async function () {
    await activePath().then((res) => {
      this.pathList = res.data;
    });

    await materialist().then((res) => {
      this.materialList = res.rows;
    });
    this.$nextTick(() => {
      this.activityInfo = JSON.parse(this.$store.state.activityView.activity);
      this.loadFeature(
        this.pathList.find(
          (item) => item.uuid == this.activityInfo.activityPath
        )
      );
    });
  },
  methods: {
    setCost(val) {
      if (val) {
        if (parseInt(val) > 100000) {
          return (parseInt(val) / 10000).toFixed(2) + "万元";
        } else {
          return parseInt(val).toFixed(2) + "元";
        }
      } else {
        return "";
      }
    },
    setPath(val) {
      if (val) {
        return this.pathList.find((c) => c.uuid == val).publicityName;
      } else {
        return "";
      }
    },
    setMaterialType(val) {
      if (val) {
        let mater_ = this.materialList.find((c) => c.uuid == val);
        return mater_.materialType + " " + mater_.name;
      } else {
        return "";
      }
    },
    setTime(time) {
      if (time) {
        let time_ = new Date(time);
        return (
          time_.getFullYear() +
          "-" +
          ((time_.getMonth() < 10 ? "0" : "") + time_.getMonth()) +
          "-" +
          ((time_.getDate() < 10 ? "0" : "") + time_.getDate())
        );
      } else {
        return "";
      }
    },
    mapComplete(map) {
      //地图加载完成后，响应业务信息。
      this.poiLayer = new PoiLayer(map, {
        isAddPointMoveStyle: false,
        isAddSelectEvent: false,
        normalScale: 1,
        normalOpacity: 1,
      });
    },
    loadFeature(item) {
      if (this.poiLayer) {
        this.poiLayer.refreshFeature([item]);
        this.poiLayer.zoomToExtent();
      } else {
        setTimeout(() => {
          this.loadFeature(item);
        }, 300);
      }
    },
  },
};
</script>
<style lang='scss' scoped>
.ContentLeft {
  width: 96vw;
  background-color: #f6f6f6;
  margin: 0 auto;
  height: 84vh;
  padding: 0.5vw;
  overflow-y: scroll;

  &::-webkit-scrollbar {
    display: none;
  }

  .ItemBox {
    background-color: white;
    margin-bottom: 2vw;
    padding: 0 2vw;
    .appHomeTitBox {
      height: 6.55vw;
      line-height: 6.55vw;
      font-size: 4vw;
      // margin: 0vw 0 0 1vw;
      padding-top: 1vw;
      display: flex;
      justify-content: space-between;
      .titleName {
        font-size: 5vw;
        font-weight: 600;
        color: #484848;
        padding-left: 3vw;
        position: relative;
        margin-left: 1vw;

        &::before {
          content: " ";
          position: absolute;
          height: 70%;
          width: 2px;
          top: 15%;
          left: 0;
          background-color: #484848;
        }
      }
      .GreenName {
        font-size: 3.2vw;
        font-weight: bold;
        color: #008c8a;
        margin-right: 2vw;
      }
    }
    .mTop {
      margin-top: -1vw;
      .GreenName {
        margin-top: -1vw;
      }
    }

    .appHomeContent {
      width: 100%;
      margin: 2.6vw 0 0;
      display: flex;
      flex-wrap: wrap;
      .mapBox {
        width: 100%;
        height: 40vh;
        border: 2px solid #81c2bb;
        padding: 1vw;
      }
      .contentItem {
        width: 100%;
        display: flex;
        margin-bottom: 3.42vw;
        margin-left: 1vw;
        position: relative;
        box-sizing: border-box;
        .itemLabel {
          width: 19vw;
          height: 2.8vh;
          margin-right: 0.9vw;
          padding-left: 1.8vw;
          font-size: 3.8vw;
          font-weight: 600;
          padding-left: 3vw;
          color: #44aaa9;
          position: relative;

          &::before {
            content: " ";
            position: absolute;
            height: 70%;
            width: 2px;
            top: 15%;
            left: 0;
            background-color: #44aaa9;
          }
          // text-align-last: justify;
        }
        .redSpot {
          position: absolute;
          top: 2vw;
          font-size: 15vw;
          border-radius: 100%;
          background-color: red;
          width: 1.2vw;
          height: 1.2vw;
        }
        .itemValue {
          flex: 1;
          background: #f5f8fd;
          font-size: 3.2vw;
          font-family: PingFangSC, PingFangSC-Regular;
          font-weight: 400;
          text-align: left;
          color: #868786;
          padding-left: 1vw;
          pointer-events: none;
          .addDate {
            position: relative;
            &::after {
              display: block;
              content: "";
              border-width: 1vw;
              border-style: solid;
              border-color: #00eab3 transparent transparent transparent;
              position: absolute;
              right: 3%;
              top: calc(50% - 0.25vw);
            }
          }
          input {
            width: 100%;
            border: none;
            padding: 0.5vw 0;
            background: #f5f8fd;
          }
          input::placeholder {
            font-size: 2.66vw;
          }
        }
        .w77 {
          height: 4.4vw;
          line-height: 4.4vw;
          max-width: 77vw;
        }
        .w66 {
          height: 14.4vw;
          max-width: 77vw;
          overflow-y: auto;
          div {
            display: flex;
            justify-content: space-between;
            padding: 0 3vw 0 0.5vw;
            margin-top: 0.5vw;
          }

          &::-webkit-scrollbar {
            display: none;
          }
        }
      }
      .flex2 {
        width: calc(50% - 1vw);
      }
    }
  }
}
</style>
