<template>
  <div>
    <div
      v-for="(child, childindex) in treeData"
      :key="'child' + childindex"
      class="itemchild"
    >
      <input
        v-model="child.isChecked"
        type="checkbox"
        @change="upLayer(child.isChecked, child)"
      /><span class="itemtxt" @click="flagChange(child)">{{
        child.layerName
      }}</span>
      <div v-show="child.lflag" v-if="child.serviceId" class="layerorgin">
        透明度:
        <el-slider
          v-model="child.tmd"
          @change="layeropc(child.tmd, child)"
        />
      </div>
      <childTree v-show="child.children" :tree-data="child.children"/>
    </div>
  </div>
</template>

<script>
export default {
  name: 'ChildTree',
  props: ['treeData'],
  data() {
    return {}
  },
  methods: {
    flagChange(obj) {
      this.$set(obj, 'lflag', !obj.lflag)
    },
    upLayer(isCheck, obj) {
      obj.isChecked = isCheck
      let _this = this
      if (obj.serviceId) {
        if (isCheck) {
          this.$emit('onLayerAdd', obj)
        } else {
          this.$emit('onLayerRemove', obj)
        }
      } else {
        if (obj.children) {
          obj.children.forEach( item=>{
            _this.upLayer(isCheck, item)
          })
        }
      }
    },
    layeropc(n, obj) {
      if (this.$parent.$listeners.layeropcity1) {
        this.$parent.$listeners.layeropcity1(obj, n)
      } else {
        this.$emit('onSetTransparency', obj, n)
      }
    }
  }
}
</script>

