<template>
  <div class="commonDialog" >
    <div class="contentDialog">
      <div class="close" @click ="cancel"/>
      <div class="title">多期数据对比</div>
      <div class="contentBox">
        <div class="content mDialog">
          <div class="main">
            <div class="left">
              <div v-for="(item ,index ) in sels">
                <span class="mLayerName">{{getLayerName(item)}}</span><span class="delete"><a @click="deleteLayer(index)">删除</a></span>
                <el-select v-model="item.data" :ref="'mSel' + index"  class="mSeletor" placeholder="请选择图层类型" style="width: 200px" @change="layerSelect(item.data ,index )">
                  <el-option
                    v-for="(layer,_index) in mulitData"
                    :key="layer.id"
                    :label="layer.name"
                    :value="layer.id"
                  ></el-option>
                </el-select>
              </div>
              <el-button class="mAddButton" size="mini" type="primary" icon="el-icon-search" @click="addCompareLayer">添加
              </el-button>
            </div>
            <div class="right">
              <div v-for="( item ,index) in sels" class="map">
                <mini-map :map-id="'map' + index "  :index="index" :layer="item.layer"
                          @syncMapAction = "syncMapAction"
                          @onMapInit = "onMapInit"></mini-map>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import MiniMap from './MiniMap'
import {queryMulitLayers} from '@/api/gis/mapservice'
export default {
  name: 'MulitMapDialog',
  components: { MiniMap },
  data() {
    return {
      // mulitData: [{ 'layerName': '2003年影像' ,id:1 }, { 'layerName': '2004年影像',id:2 }, { 'layerName': '2005年影像',id: 3 }, { 'layerName': '2006年影像' ,id:4}, { 'layerName': '2007年影像',id:5 }, { 'layerName': '2008年影像' ,id:6}]
      mulitData: [],
      sels:[],
      maps:[]
    }
  },
  watch:{
  },
  methods: {
    onMapInit( map , index ){
      this.addMap(map , index)
    },
    cancel(){
      this.$parent.showOrHideMulitMap()
    },
    setVisible(isVisible){
      if( !isVisible ){
        this.sels.forEach(item =>{
          if( item.map ){
            item.map.clear()
          }
        })
      }
      this.sels = []
      if( this.mulitData.length == 0 ){
        queryMulitLayers({isMulit:'Y'}).then(res=>{
          if( res.code === 200){
            this.mulitData = res.data
            // 默认添加两期数据
            this.sels.push({index:(this.sels.length + 1) ,data: this.mulitData[0].id ,layer:this.mulitData[0]})
            this.sels.push({index:(this.sels.length + 1) ,data: this.mulitData[0].id ,layer:this.mulitData[0]})
          }else{
            this.msgError(res.msg)
          }
        })
      }else{
        this.sels.push({index:(this.sels.length + 1) ,data: this.mulitData[0].id ,layer:this.mulitData[0]})
        this.sels.push({index:(this.sels.length + 1) ,data: this.mulitData[0].id ,layer:this.mulitData[0]})
      }
    },
    addCompareLayer() {
      if( this.sels.length > 4 ){
        this.msgError('最多只能添加5个图层数据')
        return
      }
      this.sels.push({index:(this.sels.length + 1) ,data: this.mulitData[0].id ,layer:this.mulitData[0]})
    },
    layerSelect(id ,_index) {
      this.mulitData.forEach(item =>{
        if( id == item.id){
          this.sels[_index].layer = item
          if( this.sels[_index].map ){
            this.sels[_index].map.updateLayer(item)
          }
        }
      })
    },
    getLayerName(sel){
      let layerName = sel.layer === undefined ?'请选择图层':sel.layer.name
      return sel.index +'.'+ layerName
    },
    addMap(map ,index){
      this.sels[index].map = map
    },
    deleteLayer(index){
      this.sels.splice( index , 1)
    },
    syncMapAction(zoom,center){
      this.sels.forEach(item =>{
        if( item.map ){
          item.map.syncMapAction(center,zoom)
        }
      })
    }
  }
}
</script>
