import {Text, Icon, Fill, Stroke, Style, Circle as CircleStyle,} from 'ol/style'

export default class SkywayStyle {
  constructor(params) {
    this.normalScale=params.normalScale||0.7;
    this.normalOpacity=params.normalOpacity||0.7;
  }
  /**
   * 设定常规状态下的图标
   * @param markerName  图标名称
   * @param featureType  要素类型  宣教点  宣教馆  宣教路径
   * @returns {*}
   */
  setNormalStyle(markerName,featureType){
    switch (featureType) {
      case "1":
      case "2":
      case "4":
        return new Style({
          image: new Icon({
            opacity:this.normalOpacity,
            anchor: [0.5, 44],
            anchorXUnits: 'fraction',
            anchorYUnits: 'pixels',
            src: require('@/assets/map/popularScience/' + markerName + '.png'),
            scale:[this.normalScale,this.normalScale]
          })
        })
      case "3":
        return new Style({
          stroke:new Stroke({
            color:markerName.color,
            width:2,
            lineDash:[15,5],
            // lineDashOffset:10,
          })
        })
    }

  }

  /**
   * 选中、鼠标滑过时样式
   * @param markerName
   * @returns {*}
   */
  setNormalStyleScale(markerName,featureType){
    if(markerName === 'xjg'){
      markerName='xjg_hover';
    }
    switch (featureType) {
      case "1":
      case "2":
      case "4":
        return new Style({
          image: new Icon({
            anchor: [0.5, 44],
            anchorXUnits: 'fraction',
            anchorYUnits: 'pixels',
            src: require('@/assets/map/popularScience/' + markerName + '.png'),
          })
        })
      case "3":
        return new Style({
          stroke:new Stroke({
            color:markerName.color,
            width:5,
            lineDash:[15,5]
          })
        })
    }
  }

}
