import request from '@/utils/request'

// 查询地图图层列表
export function listMaplayer(query) {
  return request({
    url: '/gis/maplayer/buildTree',
    method: 'get',
    params: query
  })
}

// 查询地图图层详细
export function getMaplayer(id) {
  return request({
    url: '/gis/maplayer/' + id,
    method: 'get'
  })
}

// 新增地图图层
export function addMaplayer(data) {
  return request({
    url: '/gis/maplayer',
    method: 'post',
    data: data
  })
}

// 修改地图图层
export function updateMaplayer(data) {
  return request({
    url: '/gis/maplayer',
    method: 'put',
    data: data
  })
}

// 删除地图图层
export function delMaplayer(id) {
  return request({
    url: '/gis/maplayer/' + id,
    method: 'delete'
  })
}

// 导出地图图层
export function exportMaplayer(query) {
  return request({
    url: '/gis/maplayer/export',
    method: 'get',
    params: query
  })
}

export function queryApplicationsByLayerId(query) {
  return request({
    url: '/gis/maplayer/queryApplicationsByLayerId',
    method: 'get',
    params: query
  })
}

// 根据应用查询图层列表
export function buildLayerTreeByApplication(query) {
  return request({
    url: '/gis/maplayer/buildLayerTreeByApplication',
    method: 'get',
    params: query
  })
}

