import { Draw } from 'ol/interaction'
import { createRegularPolygon , createBox } from 'ol/interaction/Draw'
import WKT from 'ol/format/WKT'
import { Vector as VectorLayer } from 'ol/layer'
import { Vector as VectorSource } from 'ol/source'
import { Fill, Stroke, Style, Circle, Icon, Text } from 'ol/style'
import html2canvas from 'html2canvas'

/**
 * i建查询
 * @param map
 * @constructor
 */
export default function OlMapExport(map) {
  this.map = map
  this.exportMap3 = function(mapId) {
    var _this = this
    // _this.map.once("rendercomplete",
      var download = function() {

      var mapCanvas = document.createElement("canvas");
      var size = _this.map.getSize();
      mapCanvas.width = size[0];
      mapCanvas.height = size[1];
      var mapContext = mapCanvas.getContext("2d");
      var canvasArr = document.querySelectorAll('#'+mapId+' canvas')
      // mapContext.drawImage(canvasArr[1], 0, 0);
      for( var i = 0 ; i <canvasArr.length ; i++ ){
        var canvas = canvasArr[i]
        if (canvas.width > 0) {
          const opacity = canvas.parentNode.style.opacity;
          mapContext.globalAlpha = opacity === '' ? 1 : Number(opacity);
          const transform = canvas.style.transform;
          // Get the transform parameters from the style's transform matrix
          if( transform ){
            const matrix = transform
              .match(/^matrix\(([^\(]*)\)$/)[1]
              .split(',')
              .map(Number);
            // Apply the transform to the export map context
            CanvasRenderingContext2D.prototype.setTransform.apply(
              mapContext,
              matrix
            );
          }
          mapContext.drawImage(canvas, 0, 0);
        }
      }
      // Array.prototype.forEach.call(document.querySelectorAll('#'+mapId+' canvas'), function(
      //   canvas
      // ) {
      //   if (canvas.width > 0) {
      //     var opacity = canvas.parentNode.style.opacity;
      //     var transform = canvas.style.transform;
      //     var img = new Image();
      //     img.crossOrigin = 'Anonymous'; //解决跨域问题
      //     img.onload = function() {
      //       mapContext.globalAlpha = opacity === "" ? 1 : Number(opacity);
      //       if (transform) {
      //         // Get the transform parameters from the style's transform matrix
      //         var matrix = transform
      //           .match(/^matrix\(([^\(]*)\)$/)[1]
      //           .split(",")
      //           .map(Number);
      //       }
      //       // Apply the transform to the export map context
      //       CanvasRenderingContext2D.prototype.setTransform.apply(
      //         mapContext,
      //         matrix
      //       );
      //       mapContext.drawImage(img, 0, 0);
      //     };
      //     img.src = canvas.toDataURL();
      //   }
      // });
      setTimeout(function() {
        if (navigator.msSaveBlob) {
          // link download attribuute does not work on MS browsers
          navigator.msSaveBlob(mapCanvas.msToBlob(), "map.png");
        } else {
          var link = document.getElementById("image-download");
          link.href = mapCanvas.toDataURL();
          link.click();
        }
      }, 0);
    }
    // );
    _this.map.renderSync();
    download()
  }
  this.exportMap = function(mapId) {
    var _this = this
    // _this.map.once("rendercomplete",
    var download = function() {
      var canvasArr = document.querySelectorAll('#'+mapId+'_skyway')
      html2canvas(canvasArr[0]).then(mapCanvas => {
        let dataURL = mapCanvas.toDataURL("image/png");
        setTimeout(function() {
          if (navigator.msSaveBlob) {
            navigator.msSaveBlob(mapCanvas.msToBlob(), "map.png");
          } else {
            var link = document.getElementById("image-download");
            link.href = mapCanvas.toDataURL();
            link.click();
          }
        }, 0);
      });
    }
    _this.map.renderSync();
    download()
  }
  this.exportMap2 = function(mapId,imageId,name,callback) {
    var _this = this
    var download = function() {
      var canvasList = document.querySelectorAll('#'+mapId+'_skymap')
      var mCanvas = document.createElement("canvas");
      var size = _this.map.getSize();
      mCanvas.width = size[0] + 20;
      mCanvas.height = size[1] + 80;
      var mapContext = mCanvas.getContext("2d");
      html2canvas(canvasList[0]).then(mapCanvas => {
        // 绘制边框
        //设置线宽
        mapContext.lineWidth = 2;
        //设置线的颜色
        mapContext.strokeStyle ="black";
        //画一个空心的矩形，
        mapContext.strokeRect(0,0,mCanvas.width  ,mCanvas.height);
        // 绘制标题
        mapContext.font ='bolder 35px 黑体';
        mapContext.fillText(name,(mCanvas.width  - _this.getFontLength(name , 35)) / 2 , 50 )
        // 绘制图片
        // mapContext.drawImage(canvas, 10 , 70 ,mapCanvas.width - 20 , mapCanvas.height - 80 );
        mapContext.drawImage(mapCanvas, 10 , 70 ,mCanvas.width - 20 , mCanvas.height - 80 );

        setTimeout(function() {
          if (navigator.msSaveBlob) {
            navigator.msSaveBlob(mCanvas.msToBlob(), "map.png");
          } else {
            if( callback && callback instanceof Function){
              callback( mCanvas.toDataURL() )
            }
          }
        }, 0);
      });
    }
    _this.map.renderSync();
    download()
  }

  this.exportMap3 = function(mapId,imageId,name,callback) {
    var _this = this
    var download = function() {
      var mapCanvas = document.createElement("canvas");
      var size = _this.map.getSize();
      mapCanvas.width = size[0] + 20;
      mapCanvas.height = size[1] + 80;
      var mapContext = mapCanvas.getContext("2d");
      var canvasArr = document.querySelectorAll('#'+mapId+' canvas')
      for( var i = 0 ; i <canvasArr.length ; i++ ){
        var canvas = canvasArr[i]
        if (canvas.width > 0) {
          const opacity = canvas.parentNode.style.opacity;
          mapContext.globalAlpha = opacity === '' ? 1 : Number(opacity);
          const transform = canvas.style.transform;
          if( transform ){
            const matrix = transform
              .match(/^matrix\(([^\(]*)\)$/)[1]
              .split(',')
              .map(Number);
            CanvasRenderingContext2D.prototype.setTransform.apply(
              mapContext,
              matrix
            );
          }
          // 绘制边框
          //设置线宽
          mapContext.lineWidth = 2;
          //设置线的颜色
          mapContext.strokeStyle ="black";
          //画一个空心的矩形，
          mapContext.strokeRect(0,0,mapCanvas.width  ,mapCanvas.height);
          // 绘制标题
          mapContext.font ='bolder 35px 黑体';
          mapContext.fillText(name,(mapCanvas.width  - _this.getFontLength(name , 35)) / 2 , 50 )
          // 绘制图片
          // mapContext.drawImage(canvas, 10 , 70 ,mapCanvas.width - 20 , mapCanvas.height - 80 );
          mapContext.drawImage(canvas, 10 , 70 ,mapCanvas.width - 20 , mapCanvas.height - 80 );
        }
      }
      setTimeout(function() {
        if (navigator.msSaveBlob) {
          navigator.msSaveBlob(mapCanvas.msToBlob(), "map.png");
        } else {
          // var link = document.getElementById(imageId);
          // link.href = mapCanvas.toDataURL();
          if( callback && callback instanceof Function){
            callback( mapCanvas.toDataURL() )
          }
          // link.click();
        }
      }, 0);
    }
    // );
    _this.map.renderSync();
    download()
  }

  this.getFontLength = function(str , fontSize){
    let result = 0;
    let ele = document.createElement("div");
    ele.style.position = "absolute";
    ele.style.whiteSpace = "nowrap";
    ele.style.fontSize = fontSize;
    ele.style.opacity = 0;
    ele.innerText = str;
    document.body.append(ele);
    result = ele.getBoundingClientRect().width;
    document.body.removeChild(ele);
    return result;
  }
}
