import request from '@/utils/request'

// 查询maplabel列表
export function listMaplabel(query) {
  return request({
    url: '/gis/maplabel/list',
    method: 'get',
    params: query
  })
}

// 查询maplabel详细
export function getMaplabel(id) {
  return request({
    url: '/gis/maplabel/' + id,
    method: 'get'
  })
}

// 新增maplabel
export function addMaplabel(data) {
  return request({
    url: '/gis/maplabel',
    method: 'post',
    data: data
  })
}

// 修改maplabel
export function updateMaplabel(data) {
  return request({
    url: '/gis/maplabel',
    method: 'put',
    data: data
  })
}

// 删除maplabel
export function delMaplabel(id) {
  return request({
    url: '/gis/maplabel/' + id,
    method: 'delete'
  })
}

// 导出maplabel
export function exportMaplabel(query) {
  return request({
    url: '/gis/maplabel/export',
    method: 'get',
    params: query
  })
}

export function uploadFileBase64(data) {
  return request({
    url: '/file/uploadBase64',
    method: 'post',
    data: data
  })
}

