<template>
  <div class="tweentContentBox">
    <div
      class="detailContent"
      v-if="activityInfo.tpopularScienceBActivityTweetVo.author"
    >
      <div class="TopH2">
        <!-- <div class="h2">
          {{ activityInfo.tpopularScienceBActivityTweetVo.tweetTitle || "" }}
        </div> -->
        <div class="topSmall">
          {{ setTimeText(activityInfo.tpopularScienceBActivityTweetVo) }}
        </div>
        <div class="topSmall">
          发布人:
          {{ activityInfo.tpopularScienceBActivityTweetVo.author || "" }}
        </div>
      </div>
      <div
        class="ContentText"
        v-html="
          setHtml(activityInfo.tpopularScienceBActivityTweetVo.tweetContent)
        "
      ></div>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      activityInfo: {
        tpopularScienceBActivityTweetVo: {
          author: "",
          tweetTitle: "",
          tweetContent: "",
        },
      },
      weeks: [
        "星期日",
        "星期一",
        "星期二",
        "星期三",
        "星期四",
        "星期五",
        "星期六",
      ],
    };
  },
  methods: {
    setHtml(tweetContent) {
      return tweetContent.replace(
        process.env.VUE_APP_ACCESSORY_REPLACE,
        process.env.VUE_APP_ACCESSORY_URL
      );
    },
    setTimeText(item) {
      let time = "";
      if (item.updateTime) {
        time = item.updateTime;
      } else {
        time = item.createTime;
      }

      if (time) {
        let time_ = new Date(time);
        return (
          time_.getFullYear() +
          "年" +
          ((time_.getMonth() < 9 ? "0" : "") + (time_.getMonth() + 1)) +
          "月" +
          ((time_.getDate() < 10 ? "0" : "") + time_.getDate()) +
          "日  " +
          this.weeks[time_.getDay()]
        );
      } else {
        return "";
      }
    },
  },
  created() {
    this.activityInfo = JSON.parse(this.$store.state.activityView.activity);
  },
};
</script>
<style scoped lang="scss">
.tweentContentBox {
  width: 100%;
  .detailContent {
    width: calc(100% - 4vw);
    margin: 0 auto;
    .TopH2 {
      height: 15%;
      padding: 3vw;
      color: #4a4a4a;
      box-sizing: border-box;
      background-color: #f9f9f9;
      .h2 {
        font-size: 4vw;
        font-weight: bold;
      }
      .topSmall {
        margin-top: 0.3vw;
        font-size: 4.2vw;
        font-weight: 600;
      }
    }
    .ContentText {
      height: calc(85% - 2vw);
      overflow-y: auto;
      padding: 3vw 3vw 3vw;
      // font-size: 3.3vw;
      -webkit-box-sizing: border-box;
      box-sizing: border-box;
      background-color: #f9f9f9;
      margin: 1vw 0;

      &::-webkit-scrollbar {
        display: none;
      }
      .ContentImgBox {
        width: 100%;
        height: 20vh;
        display: flex;
        li:nth-child(1) {
          width: 60%;
          img {
            width: 100%;
            height: 100%;
          }
        }
        li:nth-child(2) {
          width: 40%;
          margin-left: 0.5vw;
          div {
            width: 100%;
            height: 50%;
            img {
              height: 100%;
              width: 100%;
            }
          }
          div:nth-child(2) {
            margin-top: 0.3vw;
          }
        }
      }
      .p2 {
        margin-top: 4vw;
      }
    }
  }
}
</style>