<script>
export default {
  name: 'MapQueryResult',
  props:{
    defaultRigth:{
      type:Number,
      default:21.19,
      require:false
    }
  },
  data() {
    return {
      expand: true,
      features:[]
    }
  },
  methods:{
    expandMapComponent( isExpand ){
      this.expand = isExpand
    },
    getSymbolStyle(){
      if( !this.expand ){
        return 'transform: translateX('+ this.defaultRigth +'vw);'
      }
    },
    showFeature(features){
      this.features = features
    },
    location(feature){
      if( feature.wkt ){
        this.$parent.location(feature.wkt , true )
      }
    }
  }
