import Overlay from 'ol/Overlay'
import axios from 'axios'
import { Draw } from 'ol/interaction'
import WKT from 'ol/format/WKT'
import GeoJSON from 'ol/format/GeoJSON'



/**
 * i建查询
 * @param map
 * @constructor
 */
export default function OLMapQuery(map, layerManager) {
  this.map = map
  this.layerManager = layerManager
  this.draw = null
  this.startQuery = function(queryType ,callback) {
    this.initDraw(queryType,callback)
  }
  this.initDraw = function(drawType,callback) {
    var geometryFunc = null
    if (this.draw != null) {
      this.map.removeInteraction(this.draw)
    }
    this.draw = new Draw({
      type: drawType,
      geometryFunction: geometryFunc
    })
    this.map.addInteraction(this.draw)
    this.draw.on('drawend', e => {
      var geo = e.feature.getGeometry()
      var geometryWkt = new WKT().writeGeometry(geo)
      let resultData = this.query(geometryWkt)
      callback(resultData)
    })
  }
  this.stopQuery = function(){
    if (this.draw != null) {
      this.map.removeInteraction(this.draw)
    }
  }
  this.query = function(geometryWkt) {
    let data = []
    let currentlayers = this.layerManager.getCurrentConfigLayers()
    for (var index = 0; index < currentlayers.length; index++) {
      var configLayer = currentlayers[index]
      if (configLayer.isSupportIquery !== 'Y') continue
      if (!(configLayer.type === '3' || configLayer.type === '4')) continue
      if (configLayer.layerType !== '1') continue
      let geometryColumnName = this.findGeometryColumnName(configLayer)
      let features = this.queryFeatureByWkt(geometryWkt,configLayer.url , geometryColumnName)
      if( features && features.length > 0 ){
        features.forEach( feature=>{
          let geojson = JSON.stringify(feature.geometry)
          let tempFeature = new GeoJSON().readFeature(geojson)
          let wktstring = new WKT().writeFeature(tempFeature)
          feature.confLayer = configLayer
          feature.attrs = this.buildFeatureAttribute(configLayer, feature.properties)
          feature.wkt = wktstring
          data.push(feature)
        })
      }
    }
    return data
  }
  this.queryFeatureByWkt = function(wkt ,serviceUrl ,geometryColumnName) {
    let urlwms = serviceUrl
    let strs = urlwms.split('/')
    let typename = strs[strs.length - 3] + ':' + strs[strs.length - 2]
    let urlwfs = urlwms.replace(strs[strs.length - 1], 'wfs')
    let cql_filter = 'INTERSECTS ( '+ geometryColumnName +' ,' + wkt + ')'
    let params = {
      srsName: 'EPSG:4326',
      service: 'WFS',
      version: '1.1.0',
      request: 'GetFeature',
      typename: typename,
      CQL_FILTER: cql_filter,
      outputFormat: 'application/json'
    }
    let url = urlwfs
    url = url + '?'
    for (var key in params) {
      url = url + key + '=' + params[key] + '&'
    }
    url = url.substr(0, url.length - 1)
    let data = this.querResult(url)
    let features = JSON.parse(data).features
    return features
  }

  this.querResult = function(url){
    let xmlhttp = new XMLHttpRequest();
    xmlhttp.open("GET",url,false);
    xmlhttp.send(null);
    return xmlhttp.responseText
  }

  this.content = null
  this.overlay = null
  this.initOverlayer = function() {
    var container = document.getElementById('popup')
    this.content = document.getElementById('popup-content')
    var closer = document.getElementById('popup-closer')
    this.overlay = new Overlay({
      element: container,
      autoPan: true,
      autoPanAnimation: {
        duration: 250
      }
    })
    this.map.addOverlay(this.overlay)
    var _this = this
    closer.onclick = function() {
      _this.overlay.setPosition(undefined)
      closer.blur()
      return false
    }
  }

  this.mapClick = async function(evt) {
    var coordinate = evt.coordinate
    var currentlayers = this.layerManager.getCurrentConfigLayers()
    var view = this.map.getView()
    var viewResolution = view.getResolution()
    console.log('当前打开图层数 = ' + currentlayers.length)
    for (var index = 0; index < currentlayers.length; index++) {
      var configLayer = currentlayers[index]
      // var layerName = configLayer.layerName
      if (configLayer.isSupportIquery !== 1) continue
      if (!(configLayer.type === '3' || configLayer.type === '4')) continue
      if (configLayer.layerType !== '1') continue
      if (!configLayer.layer) continue
      var url = configLayer.layer.getSource().getGetFeatureInfoUrl(
        evt.coordinate, viewResolution, view.getProjection(), {
          'INFO_FORMAT': 'application/json',
          format_options: 'callback: getJson'
        })
      var _this = this
      const res = await axios.get(url)
      const collectionFeature = res.data
      if (collectionFeature) {
        if (collectionFeature.features && collectionFeature.features.length > 0) {
          const featureproperties = collectionFeature.features[0].properties
          console.log(JSON.stringify(featureproperties))
          var featureAttr = _this.buildFeatureAttribute(configLayer, featureproperties)
          console.log(featureAttr)
          _this.showAttribute(featureAttr)
          _this.overlay.setPosition(coordinate)
          return
        }
      }
    }
  }
  this.showAttribute = function(featureAttr) {
    var html = ''
    featureAttr.forEach(item => {
      html = html + '<div class="column"><span>' + item.nameZh + ':&nbsp</span><span>' + item.value + '</span></div>'
    })
    this.content.innerHTML = html
  }
  this.buildFeatureAttribute = function(configLayer, data) {
    var iQueryConfig = configLayer.iqueryConfig
    try {
      if (iQueryConfig && iQueryConfig.length > 0) {
        var layerColumns = JSON.parse(iQueryConfig)
        var columns = layerColumns.colums
        columns = this.sortingColumn(columns)
        if (!columns || columns.length === 0) return []
        var result = []
        for (var _index = 0; _index < columns.length; _index++) {
          var column = columns[_index]
          if (!column.isShow) continue
          var value = data[column.name]
          if (value === null || value === undefined) {
            value = '未知'
          }
          result.push({ 'nameZh': column.nameZh, 'value': value + column.unit })
        }
        return result
      }
    } catch (e) {
      return []
    }
  }
  /**
   * 查询服务中空间字段的名称信息
   * @param configLayer
   * @returns {string}
   */
  this.findGeometryColumnName = function(configLayer){
    let geometryName = 'the_geom'
    var iQueryConfig = configLayer.iqueryConfig
    try {
      if (iQueryConfig && iQueryConfig.length > 0) {
        var layerColumns = JSON.parse(iQueryConfig)
        var columns = layerColumns.colums
        if (!columns || columns.length === 0) return geometryName

        for (var _index = 0; _index < columns.length; _index++) {
          var column = columns[_index]
          if (column.localType !== 'Geometry' ) continue
          geometryName = column.name
        }
        return geometryName
      }
    } catch (e) {
      return geometryName
    }
  }

  this.sortingColumn = function(columns) {
    function compare(a, b) {
      return a.sort - b.sort
    }

    return columns.sort(compare)
  }
}
