<template>
  <div class="commonDialog" >
    <div class="contentDialog">
      <div class="close" @click ="cancel"/>
      <div class="title">地图输出</div>
      <div class="contentBox">
        <div class="content mDialog">
          <div class="left">
            <img ref="mImage" id="mExportMapImage" style="width: 100%;height: 100%;">
          </div>
          <div class="right">
            <div class="top">
              <span>名称:</span>
              <el-input v-model="form.name" class="mInput" ></el-input>
            </div>
            <div class="bottom">
              <el-button class="filter-item mButton" size="mini" type="primary" @click="refresh">刷新</el-button>
              <el-button class="filter-item mButton" size="mini" type="primary" @click="downloadMap">下载</el-button>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'landMark',
  props:{

  },
  data() {
    return {
      isAdd:false,
      form:{
        name:'',
        mark:''
      }
    }
  },
  methods:{
    cancel(){
      this.$parent.showOrHideExportMap()
    },
    showImage(data){
      this.$refs.mImage.src = data
    },
    downloadMap(){
      let _this = this
      var image = new Image()
      // 解决跨域 Canvas 污染问题
      // crossorigin 是HTML5中新增的<img>标签属性
      //　crossorigin属性有两个值可选：
      //anonymous:如果使用这个值的话就会在请求中的header中的带上origin属性，但请求不会带上cookie和其他的一些认证信息。
      //use-credentials:这个同时会在跨域请求中带上cookie和其他的一些认证信息。在使用这两个值时都需要server端在response的header中带上Access-Control-Allow-Credentials属性。可以通过server的配置文件来开启这个属性：server开启Access-Control-Allow-Credentials
      image.setAttribute('crossOrigin', 'anonymous')
      image.onload = function () {
        var canvas = document.createElement('canvas')
        canvas.width = image.width
        canvas.height = image.height

        var context = canvas.getContext('2d')
        context.drawImage(image, 0, 0, image.width, image.height)
        var url = canvas.toDataURL('image/png')
        // 生成一个a元素
        var a = document.createElement('a')
        // 创建一个单击事件
        var event = new MouseEvent('click')

        // 将a的download属性设置为我们想要下载的图片名称，若name不存在则使用‘下载图片名称’作为默认名称
        a.download = name || _this.form.name
        // 将生成的URL设置为a.href属性
        a.href = url

        // 触发a的单击事件
        a.dispatchEvent(event);
      }
      image.src =  this.$refs.mImage.src
    },
    refresh(){
      this.$parent.exportMap(this.form.name)
    }
  }
}
</script>
