<template>
  <!--  图层框  -->
  <div v-show="isShowLayer" class="mLayer" :style="getSymbolStyle()">
    <div class="layersearch">
      <input v-model="name" type="text" placeholder="请输入图层名称"/>
      <span class="el-icon-search" @click="searchTree"/>
    </div>
    <div class="layertree">
      <div class="treeitem">
        <el-scrollbar>
          <div v-for="(item, index) in treeData" :key="index" class="item1">
            <div class="itemparent">
                <span class="lt" @click="flagChange(item)">
                  {{ item.layerName }}
                </span>
              <span
                v-if="item.lflag"
                class="el-icon-caret-bottom btm"
                @click="flagChange(item)"
              />
              <span
                v-else
                class="el-icon-caret-left btm"
                @click="flagChange(item)"
              />
            </div>
            <template v-if="item.lflag">
              <child-tree
                ref="treeC"
                :tree-data="item.children"
                @onLayerAdd="onLayerAdd"
                @onLayerRemove="onLayerRemove"
                @onSetTransparency="onSetTransparency"
              />
            </template>
          </div>
        </el-scrollbar>
      </div>
    </div>
  </div>
</template>

<script>
import { listMaplayer as buildMapLayerTree ,buildLayerTreeByApplication} from '@/api/gis/maplayer'
import childTree from '../childTree'
import Cookies from "js-cookie";
export default {
  name: 'LayerManager',
  components: { childTree},
  props: {
    defaultRigth: {
      type: Number,
      default: 31.2,
      require: false
    }
  },
  data() {
    return {
      expand: true,
      isShowLayer: false,
      treeData: [],
      map: null,
      name: '',
      layerManager: null,
      mDrawLayer:null ,
      mSelectLayers:[]
    }
  },
  mounted() {
    var _this = this
    document.addEventListener('click' ,(e)=>{
      let elements = e.path
      let thisClassName = e.target.className
      let inThis = false
      elements.forEach((className, index)=>{
        if( className.className ){
          let classTargetName = className.className.toString()
          if( classTargetName.indexOf('mLayer') > -1 && index > 0 ){
              inThis = true
          }
        }
      })
      //console.log(inThis)
      if( thisClassName === 'leftItem classLayer'){
        _this.isShowLayer = !_this.isShowLayer
      }else if(!inThis){
        _this.isShowLayer = false
      }
    })
  },
  methods: {
    expandMapComponent(isExpand) {
      this.expand = isExpand
    },
    getSymbolStyle() {
      if (!this.expand) {
        return 'transform: translateX(' + 21 + 'vw)'
      }
    },
    onExpandClick() {
      this.expand = !this.expand
      this.$emit('onExpandClick', this.expand)
    },
    showLayerManager() {
      this.isShowLayer = !this.isShowLayer
    },
    onLayerAdd(configLayer) {
      console.log('addLayer' + configLayer.layerName)
      // 参数还原
      configLayer.cql_filter = configLayer.mparams
      this.layerManager.addConfigLayer(configLayer, true)
    },
    onLayerRemove(configLayer) {
      console.log('removeLayer' + configLayer.layerName)
      this.layerManager.removeLayer(configLayer)
      this.layerManager.clearLastHighLightConfigLayer()
    },
    onSetTransparency(configLayer, num) {
     // console.log('setLayerCopicty')
      this.layerManager.setLayerOpactiy(configLayer, num / 100)
    },
    flagChange(obj) {
      this.$set(obj, 'lflag', !obj.lflag)
    },
    searchTree() {
      this.initMapLayer()
    },
    initMap(map, layerManager) {
      this.map = map
      this.layerManager = layerManager
    },
    initMapLayer() {
      const apply = Cookies.get("apply");
      buildLayerTreeByApplication({ name: this.name ,applications:[apply] }).then(res => {
        if (res.code == 200) {
          res.data.forEach((configLayer) => {
            this.initLayer(configLayer)
          })
          this.treeData = res.data;
        }
      })
    },
    initLayer(configLayer) {
      configLayer.isChecked = false
      if (configLayer.serviceId) {
        configLayer.tmd = Number(configLayer.defaultTransparency)
        if (configLayer.isAutoLoad === 'Y'){
          configLayer.isChecked = true
          this.layerManager.addConfigLayer(configLayer, false)
        }
      } else {
        // 图层组
        if (configLayer.children && configLayer.children.length > 0) {
          configLayer.children.forEach((item) => {
            this.initLayer(item)
          })
        }
      }
    },
    /**
     * 显示或隐藏图层
     * @param layerNames 为null 或者[] 时关闭所有之前打开的图层
     * @param isChecked
     */
    setLayerSelect(selectLayers , isChecked , isHighLight){
      console.log(selectLayers + '&' + isHighLight + "&"+isChecked )
      if( selectLayers && selectLayers.length > 0 ){
        if( isChecked && !isHighLight){
          this.mSelectLayers.push.apply(this.mSelectLayers,selectLayers)
        }
        selectLayers.forEach( selectLayer =>{
          this.treeData.forEach( _item=>{
            this.selectLayer(_item ,selectLayer ,isChecked ,isHighLight)
          })
        })
      }else{
        this.layerManager.clearLastHighLightConfigLayer()
        if(this.mSelectLayers.length > 0 ){
          this.setLayerSelect( this.mSelectLayers , false) ;
          this.mSelectLayers.splice(0 , this.mSelectLayers.length) ;
        }
      }
    },
    selectLayer(configLayer ,selectLayer ,isChecked ,isHighLight){
      if (configLayer.serviceId){
        if( configLayer.layerName === selectLayer.layerName){
          if( isChecked ){
              configLayer.isChecked = true
              // 临时参数赋值
              if( selectLayer.oparams ){
                configLayer.cql_filter = selectLayer.oparams
              }else{
                configLayer.cql_filter = selectLayer.mparams
              }
              this.layerManager.addConfigLayer(configLayer, false)
          }else{
            if( !isHighLight ){
              configLayer.isChecked = false
              this.layerManager.removeLayer(configLayer)
            }
          }
          if( isHighLight ){
            // 深度拷贝一个图层配置
            // 临时参数赋值
            if( selectLayer.oparams ){
              configLayer.cql_filter = selectLayer.oparams
            }else{
              configLayer.cql_filter = selectLayer.mparams
            }
            this.addHightConfigLayer(configLayer)
          }
        }
      }else{
        if (configLayer.children && configLayer.children.length > 0) {
          configLayer.children.forEach((item) => {
            this.selectLayer(item ,selectLayer ,isChecked ,isHighLight)
          })
        }
      }
    },
    addHightConfigLayer(configLayer){
      if( configLayer.hightUrl === undefined || configLayer.hightUrl === null || configLayer.hightUrl === '' ) return
      // 避免深拷贝时拷贝不需要的layer属性
      var layer = null
      if( configLayer.layer ){
        layer = configLayer.layer
        configLayer.layer = null
      }
      let newConfigLayer = this.deepClone(configLayer)
      // 避免深拷贝时拷贝不需要的layer属性
      if( layer ){
        configLayer.layer = layer
      }
      newConfigLayer.isHighLight = true
      newConfigLayer.layerName = "_" + configLayer.layerName
      newConfigLayer.url = configLayer.hightUrl
      this.layerManager.addConfigLayer(newConfigLayer, false)
    },
    getSymbolsByLayerName(layerName){
      let symbols = []
      this.getSymbolsByLayerNamePrivate(this.treeData ,layerName ,symbols)
      // console.log('--------------' + symbols.length)
      return symbols ;
    },
    getSymbolsByLayerNamePrivate(configLayers,layerName,mSymbols){
      configLayers.forEach( _item=>{
        // console.log('--------------' + _item.layerName)
        if (_item.serviceId){
            if( _item.layerName === layerName){
              let config = _item.iqueryConfig
              if (config && config.length > 0){
                var configuration = JSON.parse(config)
                var symbols = configuration.symbols
                if( symbols && symbols.length > 0 ){
                  // console.log('--------------1')
                   // return symbols
                  mSymbols.push.apply(mSymbols,symbols)
                }
              }
              // console.log('--------------3')
            }
        }else{
          if (_item.children && _item.children.length > 0) {
            this.getSymbolsByLayerNamePrivate(_item.children,layerName,mSymbols)
          }
        }
      })
      // console.log('--------------2')
    }
  }
}
</script>

