<template>
  <div class="appdetail">
    <div class="detailTop">
      <img
        @click="$router.push({ path: '/activitylist' })"
        src="@/assets/phone/AppVisitorHome/back.png"
        alt
      />
      <div>活动详情</div>
    </div>
    <div class="detailTabs">
      <div
        :class="['TabsRight', ShowFlag == '1' ? 'detailTabsBom' : '']"
        @click="ShowFlag = '1'"
      >
        活动信息
      </div>
      <div
        v-if="activityInfo.tpopularScienceBActivityTweetVo.author"
        :class="['TabsRight', ShowFlag == '4' ? 'detailTabsBom' : '']"
        @click="ShowFlag = '4'"
      >
        活动推文
      </div>
      <div
        :class="['TabsRight', ShowFlag == '2' ? 'detailTabsBom' : '']"
        @click="ShowFlag = '2'"
      >
        活动资料库
      </div>
    </div>
    <div
      class="detailContent"
      :style="{ paddingBottom: activityInfo.enroll == 0 ? '10vh' : '' }"
    >
      <ActivityInformation
        v-if="ShowFlag == '1'"
        ref="contentLeft"
      ></ActivityInformation>
      <DatabaseActivity v-if="ShowFlag == '2'"></DatabaseActivity>
      <tweentContent v-if="ShowFlag == '4'"></tweentContent>
    </div>
    <div
      class="detailBom"
      @click="tryToApply"
      v-if="activityInfo.enroll == 0 && activityInfo.remainingPlaces != '0'"
    >
      <!-- <img @click="tryToApply" src="@/assets/phone/sign_up.png" alt /> -->
    </div>
  </div>
</template>

<script>
import ActivityInformation from "./ActivityInformation.vue";
import DatabaseActivity from "./DatabaseActivity.vue";
import tweentContent from "./tweentContent.vue";

export default {
  components: {
    ActivityInformation,
    DatabaseActivity,
    tweentContent,
  },
  data() {
    return {
      ShowFlag: "1",
      activityInfo: {},
    };
  },
  created: function () {
    this.activityInfo = JSON.parse(this.$store.state.activityView.activity);
  },
  methods: {
    tryToApply() {
      if (this.activityInfo.enroll == 0) {
        this.$router.push("/activity");
      } else {
        this.$message({
          message: "此活动暂时未开始报名!",
          type: "warning",
        });
      }
    },
  },
  mounted() {},
};
</script>
<style lang='scss' scoped>
.appdetail {
  width: 100vw;
  height: 100vh;
  background: #f6f6f6;
  .detailTop {
    width: 100vw;
    height: 10vh;
    background: linear-gradient(116deg, #41bb64 17%, #0a4fa0);
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
    img {
      width: 7vw;
      height: 7vw;
      position: absolute;
      top: 6.5vw;
      left: 3vw;
    }
    div {
      font-size: 6.5vw;
      color: white;
    }
  }
  .detailTabs {
    width: 100vw;
    height: 5vh;
    line-height: 5vh;
    display: flex;
    color: #6c6c6c;
    background: #f6f6f6;
    margin-bottom: 2vw;
    .TabsLeft,
    .TabsRight {
      flex: 1;
      font-size: 4.2vw;
      font-weight: 500;
      text-align: center;
      margin: 0 1.4vw;
      color: #6c6c6c;
    }
  }
  .detailTabsBom {
    color: black;
    font-weight: 900 !important;
    // border-bottom: 1px solid green;
    margin: 0 1.4vw;
    position: relative;
    &::after {
      content: "";
      width: 100%;
      height: 1%;
      bottom: 10%;
      left: 0;
      position: absolute;
      background: #3cc18c;
      border: 1px solid #97979741;
      border-radius: 1px;
    }
  }
  .detailContent {
    width: 100vw;
    // height: 85vh;

    box-sizing: border-box;
    // padding: 1vw 0 0 0;
    background-color: #f6f6f6;
  }
  .detailBom {
    width: 96vw;
    height: 10vh;
    position: fixed;
    left: 2vw;
    bottom: 0;
    background: url("../../../../../assets/phone/sign_up.png") no-repeat;
    background-size: 100% 100%;
    img {
      width: 100%;
      // height: 100%;
    }
  }
}
</style>
